import React, { useState, useEffect } from "react";
import TeacherDetailPresenter from "./TeacherDetailPresenter";
import { teacherAPI } from "api";
import HandleConfirm from "Components/HandleConfirm";

const TeacherDetailContainer = ({ teacherData, setModalOpen, setModalID, setModalData, modalClose }) => {
  const [teacher, setTeacher] = useState(null);

  const deleteTeacher = async (id) => {
    try {
      const {
        data: { result },
      } = await teacherAPI.deleteTeacher(id);
      if (result) {
        alert(" ");
        modalClose();
      }
    } catch {}
  };

  const handleDelete = (id) => {
    HandleConfirm("?", () => deleteTeacher(id), null);
  };

  useEffect(() => {
    setTeacher(teacherData);
  }, []);

  return (
    <TeacherDetailPresenter
      teacher={teacher}
      setModalOpen={setModalOpen}
      setModalID={setModalID}
      setModalData={setModalData}
      handleDelete={handleDelete}
    />
  );
};

export default TeacherDetailContainer;
