import styled from 'styled-components';

const StudentModifyPresenter = ({
  handleChangeGrade,
  handleChangeCategory,
  handleChangeSubjects,
  handleChangeSubjectHours,
  handleChangeOfficeHours,
  handleChangeTimes,
  handleChangePreferredTeacher,
  handleSubmit,
  student,
  subjectsOrder,
  days,
  times,
  category,
  grade,
  subjects,
  officeHours,
  // isModify,
  inputName,
  inputSchool,
  inputPhone,
  inputParents,
  teachers,
  validate,
  errors
}) => {
  
  return (
    <Main>
      <ModalLeft>
        <ModalSection>
          <ModalTitle>  </ModalTitle>
          <ModalInput
            ref={inputName}
            defaultValue={(student && student.name) || ''}
            type="text"
            name="name"
            placeholder=""
          />

          <ModalSchool>
            <ModalInput
              ref={inputSchool}
              defaultValue={(student && student.school) || ''}
              type="text"
              name="school"
              placeholder=""
            />

            <ModalSelect onChange={handleChangeGrade} value={grade} name="grade">
              <option value="" disabled hidden>
                
              </option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="0"></option>
            </ModalSelect>
            <ModalSelect onChange={handleChangeCategory} value={category} name="category">
              <option value="" disabled hidden>
                {' '}
              </option>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </ModalSelect>
          </ModalSchool>
          <ModalInput
            ref={inputPhone}
            name="phone"
            defaultValue={(student && student.phone) || ''}
            type="text"
            placeholder=" "
          />
          <ModalInput
            ref={inputParents}
            name="parents"
            defaultValue={(student && student.parents) || ''}
            type="text"
            placeholder=" "
          />
        </ModalSection>
        <ModalSection>
          <ModalTitle> / </ModalTitle>

          {subjectsOrder.map((subject, index) => (
            <>
              <input
                type="checkbox"
                name="subjects"
                id={`subject${index}`}
                value={subject}
                onChange={handleChangeSubjects}
                checked={subjects && subjects.length > 0 && subjects.findIndex((s) => s.name === subject) > -1}
              />
              <label htmlFor={`subject${index}`}>
                <span></span>
                <div className="cboxlabel"> {subject} </div>
              </label>
            </>
          ))}

          <div className="container">
            <ul className="ks-cboxtags">
              {subjects &&
                subjects.length > 0 &&
                subjects.map((subject, index) => (
                  <li key={`studentModifySubjectButton${index}`}>
                    <ButtonGroup>
                      <PlusMinusButton value={`${subject.name} plus`} onClick={handleChangeSubjectHours}>
                        <i className="fas fa-plus"></i>{' '}
                      </PlusMinusButton>
                      {subject.name} {subject.hours}
                      <PlusMinusButton value={`${subject.name} minus`} onClick={handleChangeSubjectHours}>
                        <i className="fas fa-minus"></i>{' '}
                      </PlusMinusButton>
                    </ButtonGroup>

                    <PreferredTeacherSelectContainer>
                      <ButtonGroup>
                        <PreferredTeacherSelect
                          onChange={handleChangePreferredTeacher}
                          value={
                            subject && subject.teacher && subject.teacher._id
                              ? `${subject.name} ${subject.teacher._id}`
                              : `${subject.name} ${subject.teacher}`
                          }
                        >
                          <option value=""></option>
                          {teachers &&
                            teachers[subject.name] &&
                            teachers[subject.name].length > 0 &&
                            teachers[subject.name].map((teacher, index) => (
                              <option value={`${subject.name} ${teacher._id}`}>{teacher.name}</option>
                            ))}
                        </PreferredTeacherSelect>
                      </ButtonGroup>
                    </PreferredTeacherSelectContainer>
                  </li>
                ))}
            </ul>
          </div>
        </ModalSection>
      </ModalLeft>

      <ModalRight>
        <ModalSection>
          <ModalTitle> / </ModalTitle>
          {days.map((day, index) => (
            <span key={`studentModifyDayCheckbox${index}`}>
              <input
                type="checkbox"
                id={`day${index}`}
                name="officeHours"
                value={day}
                onChange={handleChangeOfficeHours}
                checked={
                  officeHours &&
                  officeHours.length > 0 &&
                  officeHours.findIndex((officeHour) => officeHour.day === day) > -1
                }
              />
              <label htmlFor={`day${index}`}>
                <span></span> <div className="cboxlabel"> {day} </div>{' '}
              </label>
            </span>
          ))}
          {officeHours &&
            officeHours.length > 0 &&
            officeHours.map((officeHour) => (
              <div className="container" key={`studentModifyOfficeHour${days.indexOf(officeHour.day)}`}>
                <ul className="ks-cboxtags">
                  {times.map((time, tIndex) => (
                    <li key={`studentModifyTime${tIndex}`}>
                      <input
                        type="checkbox"
                        id={`day${days.indexOf(officeHour.day)}time${tIndex}`}
                        name="times"
                        value={`${officeHour.day}${time}`}
                        onChange={handleChangeTimes}
                        // 21.11.11 :  
                        defaultChecked
                        // checked={officeHour.times.includes(time)}
                      />
                      <label htmlFor={`day${days.indexOf(officeHour.day)}time${tIndex}`}>
                        {officeHour.day}
                        {time}
                      </label>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
        </ModalSection>

        { Object.keys(errors).length === 0 
          ? null 
          : Object.keys(errors).map((key, index)=>(
            <ModalFormError> <i class="fas fa-exclamation-circle"></i> &nbsp; {errors[key]} </ModalFormError>  
          )) 
        }
        <SubmitButton onClick={validate} value="">  </SubmitButton>
      </ModalRight>
    </Main>
  );
};    


const SubmitButton = styled.button`
  display: block;
  background-color: #eef5f9;
  color: black;
  height: 58px;
  border-radius: 4px;
  margin-top: 7px;
  font-size: 15px;
  line-height: 58px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: 0;
  &:hover {
    background-color: #75bdf8;
    color: #fafafa;
    transition-duration: 0.3s;
  }
`;

const ModalSchool = styled.div`
  display: flex;
`;

const ModalTitle = styled.div`
  font-size: 14px;
  line-height: 28px;
  font-weight: 800;
  margin-bottom: 10px;
`;

const ModalInput = styled.input`
  flex-basis: 70%;
  width: 100%;
  height: 40px;
  padding-left: 17px;
  border: none;
  border-bottom: 1px solid #efefef;
  margin: 7px 0px;
`;

const ModalFormError = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #ff4e5d;
  color: #fff;
  padding: 8px;
  font-size: 13px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
`;

const ModalSelect = styled.select`
  border: none;
  padding: 5px 10px;
  font: inherit;
  width: 100%;
  font-size: 12px;
  flex-basis: 30%;
`;

// FIXME:  ..
const PreferredTeacherSelectContainer = styled.div`
  display: flex;
  justify-content: space-evenly;
`;

const PreferredTeacherSelect = styled(ModalSelect)`
  padding: 0 0px;
  color: #adadad;
  font-size: 12px;
  text-align: center;
`;

const Main = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 35px 35px 25px;
`;

const ModalLeft = styled.div`
  padding: 20px 40px;
  font-size: 13px;
`;

const ModalRight = styled.div`
  padding: 20px 40px;
  font-size: 13px;
`;

const ModalSection = styled.div`
  margin-bottom: 32px;
`;

const ButtonGroup = styled.div`
  display: inline-block;
  font-size: 12px;
  background-color: rgba(255, 255, 255, 0.9);
  border: 2px solid rgba(139, 139, 139, 0.3);
  color: #adadad;
  border-radius: 25px;
  white-space: nowrap;
  margin: 3px 0px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: all 0.2s;
  padding: 8px 8px;
  cursor: pointer;
`;

const PlusMinusButton = styled.button`
  font-size: 12px;
  border: none;
  text-align: center;
  cursor: pointer;
  background-color: inherit !important;
  color: inherit !important;
`;

export default StudentModifyPresenter;