import React, { useState, useEffect } from 'react';
import GroupPresenter from './GroupPresenter';
import HandleConfirm from 'Components/HandleConfirm';
import { groupAPI } from 'api';

const GroupContainer = () => {
  const [modalOpen, setModalOpen] = useState(false);
  const [modalID, setModalID] = useState('');
  const [modalData, setModalData] = useState(null);
  const [groups, setGroups] = useState();

  const modalClose = () => {
    setModalOpen(false);
    setModalID('');
    setModalData(null);
  };

  const getGroupsData = async () => {
    try {
      const {
        data: { groups: groupsData }
      } = await groupAPI.readAllGroups();
      setGroups(groupsData);
    } catch {}
  };



  const deleteGroup = async (id) => {
    try {
      setModalID("");
      const {
        data: { result },
      } = await groupAPI.deleteGroup(id);
      if (result) {
        alert(" ");
      }
    } catch {
    } finally {
      setModalID("FOR RERENDERING"); // trick
    }
  };

  const handleDelete = (id) => {
    HandleConfirm("?", () => deleteGroup(id), null);
  };

  useEffect(() => {
    getGroupsData();
  }, [modalOpen, modalID]);

  useState(() => {
    getGroupsData();
    groups && console.log(groups);
  }, []);

  return (
    <GroupPresenter
      groups={groups}
      modalOpen={modalOpen}
      setModalOpen={setModalOpen}
      modalID={modalID}
      setModalID={setModalID}
      modalData={modalData}
      setModalData={setModalData}
      modalClose={modalClose}
      handleDelete={handleDelete}
    />
  );
};

export default GroupContainer;
