import styled, { keyframes } from 'styled-components';
import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import { CSVLink } from 'react-csv';
import ProgressIndicator from 'Components/ProgressIndicator';

const SideButtons = ({ openShare, CSVData, CSVHeaders }) => {
  return (
    <Side>
      <PngButton openShare={openShare} onClick={onDownloadBtn}>
        <i className="fas fa-file-image"></i> png{' '}
      </PngButton>
      <XslxButton openShare={openShare}>
        <StyledCSVLink data={CSVData} headers={CSVHeaders} filename={'table.csv'}>
          <i className="fas fa-file-excel"></i> xlsx
        </StyledCSVLink>
      </XslxButton>
    </Side>
  );
};

const onDownloadBtn = () => {
  domtoimage
    .toBlob(document.querySelector('.table'), { bgcolor: 'white' })
    .then((blob) => {
      // TODO: /     ex) .png
      saveAs(blob, 'table.png');
    })
    .catch((e) => {
      console.log(e);
    });
};

const TablePresenter = ({
  loading,
  openShare,
  setOpenShare,
  table,
  active,
  setActive,
  handleToggle,
  handleRefresh,
  handleClickCell,
  updateCellsIsFixed,
  CSVHeaders,
  CSVData
}) => {
  const days = ['', '', '', '', '', '', ''];
  const times = ['A', 'B', 'C', 'D', 'E', 'F'];

  const renderTimes = () => {
    const result = [];
    for (let i = 0; i < days.length; i++) {
      times.map((time, index) =>
        result.push(
          <TableTime dIndex={i} tIndex={index}>
            {time}
          </TableTime>
        )
      );
    }
    return result;
  };

  return (
    <>
      <TableHead>
        <Left>
          {active ? (
            <CheckedBadge>
              {' '}
              <i className="fas fa-check"></i> &nbsp; {' '}
            </CheckedBadge>
          ) : null}
          <ShareButton
            onClick={() => {
              setOpenShare(!openShare);
            }}
          >
            <i className="fas fa-file-download"></i>&nbsp; 
          </ShareButton>
          <SideButtons openShare={openShare} CSVHeaders={CSVHeaders} CSVData={CSVData} />
        </Left>
        <Right>
          <Toggle
            className="toggle"
            onChange={() => {
              handleToggle();
            }}
            type="checkbox"
            checked={active}
          ></Toggle>
          <WholeDeselectButton onClick={updateCellsIsFixed}>
            <i class="fas fa-th-list"></i> &nbsp; 
          </WholeDeselectButton>
          <RefreshButton onClick={handleRefresh}>
            <i className="fas fa-redo-alt"></i> &nbsp;  
          </RefreshButton>
        </Right>
      </TableHead>

      <TableSection>
        {loading ? (
          <ProgressIndicator />
        ) : (
          <Table className="table">
            <TableCategory>  </TableCategory>
            <LectureTypeHead>  </LectureTypeHead>
            <TeacherNameHead>  </TeacherNameHead>
            {days.map((day, index) => (
              <TableDay index={index}> {day} </TableDay>
            ))}
            {renderTimes()}

            {table &&
              table.group &&
              table.group.length > 0 &&
              table.group.map((row, index) => (
                <>
                  <LectureType index={index}>  </LectureType>
                  <TeacherName index={index}>
                    {row.teacher.name && row.teacher.name} ({row.teacher.subject && row.teacher.subject})
                  </TeacherName>
                  {row.lecture &&
                    row.lecture.length > 0 &&
                    row.lecture.map((day, dIndex) => {
                      return times.map((time, tIndex) => (
                        <>
                          <TeacherStudent
                            key={`Group${tIndex}${dIndex}${index}`}
                            tIndex={tIndex}
                            dIndex={dIndex}
                            index={index}
                            startIndex={tIndex + dIndex * times.length + 4}
                            className="tooltip"
                            // TODO:   ~~  
                            data-tooltip={day[time] && day[time].students.map((student) => ` ${student.name}`)}
                          >
                            {day[time] && day[time].name ? day[time].name : null}
                          </TeacherStudent>
                        </>
                      ));
                    })}
                </>
              ))}
            {table &&
              table.personal &&
              table.personal.length > 0 &&
              table.personal.map((row, index) => (
                <>
                  <LectureType index={table.group.length + index}>  </LectureType>
                  <TeacherName index={table.group.length + index}>
                    {row.teacher.name && row.teacher.name} ({row.teacher.subject && row.teacher.subject})
                  </TeacherName>
                  {row.lecture &&
                    row.lecture.length > 0 &&
                    row.lecture.map((day, dIndex) =>
                      times.map((time, tIndex) => (
                        <TeacherStudent
                          key={`Personal${tIndex}${dIndex}${index}`}
                          tIndex={tIndex}
                          dIndex={dIndex}
                          index={table.group.length + index}
                          startIndex={tIndex + dIndex * times.length + 4}
                          data-day-index={dIndex}
                          data-time={time}
                          data-teacher-id={row.teacher._id}
                          onClick={handleClickCell}
                          isFixed={day[time] && day[time].isFixed}
                        >
                          {day[time] && day[time].student && day[time].student.name ? day[time].student.name : null}
                        </TeacherStudent>
                      ))
                    )}
                </>
              ))}
          </Table>
        )}
      </TableSection>

      <BottomSection>
        <HalfSection>
          <SectionTitle>   </SectionTitle>
          {table &&
            table.unorganized.teachers &&
            table.unorganized.teachers.map((teacher, index) => (
              <Card>
                <CardLeft>
                  {/* FIXME: teacher ID  */}
                  <CardName>{teacher.name} </CardName>
                  <CardTeacherSubject>{teacher.subject}</CardTeacherSubject>
                </CardLeft>
                <CardRight>
                  {teacher.officeHours &&
                    teacher.officeHours.map((oHour, index) => (
                      <>
                        {oHour.day}({oHour.times})
                      </>
                    ))}
                </CardRight>
              </Card>
            ))}
        </HalfSection>

        <HalfSection>
          <SectionTitle>   </SectionTitle>
          {table &&
            table.unorganized.students &&
            table.unorganized.students.map((student, index) => (
              <Card>
                <CardLeft>
                  <CardName> {student.name} </CardName>
                </CardLeft>
                <CardRight>
                  {student.subjects &&
                    student.subjects.map((subject, index) => (
                      <>
                        {subject.name}({subject.hours})
                      </>
                    ))}
                </CardRight>
              </Card>
            ))}
        </HalfSection>
      </BottomSection>
    </>
  );
};

