import axios from 'axios';

const api = axios.create({
  baseURL: process.env.REACT_APP_API_SERVER_URI
  // withCredentials: true
});

export const studentAPI = {
  createStudent: (student) => api.post('/api/students', student),
  readAllStudents: () => api.get('/api/students'),
  updateStudent: (student) => api.put('/api/students', student),
  deleteStudent: (id) => api.delete(`/api/students/${id}`)
};

export const teacherAPI = {
  createTeacher: (teacher) => api.post('/api/teachers', teacher),
  readAllTeachers: () => api.get('/api/teachers'),
  readTeachersBySubject: (subject) => api.get(`/api/teachers/subjects/${subject}`),
  updateTeacher: (teacher) => api.put('/api/teachers', teacher),
  deleteTeacher: (id) => api.delete(`/api/teachers/${id}`)
};

export const groupAPI = {
  createGroup: (group) => api.post('/api/groups', group),
  readAllGroups: () => api.get('/api/groups'),
  readGroupsBySubject: (subject) => api.get(`/api/groups/subjects/${subject}`),
  readGroupById: (id) => api.get(`/api/groups/${id}`),
  updateGroup: (group) => api.put('/api/groups', group),
  deleteGroup: (id) => api.delete(`/api/groups/${id}`)
};

export const tableAPI = {
  createTable: (existingTableId) => api.post('/api/tables', { existingTableId }),
  //   
  readTable: () => api.get('/api/tables'),
  changeActiveness: (id, active) => api.put(`/api/tables/${id}/activeness`, { active }),
  updateCellsIsFixed: (id) => api.put(`/api/tables/${id}/cells`),
  updateCellIsFixed: (id, dayIndex, time, teacherId) => api.put(`/api/tables/${id}/cell`, { dayIndex, time, teacherId })
};
export default api;
