import React, { useState, useEffect } from "react";
import StudentDetailPresenter from "./StudentDetailPresenter";
import { studentAPI } from "api";
import HandleConfirm from "Components/HandleConfirm";

const StudentDetailContainer = ({ studentData, setModalOpen, setModalID, setModalData, modalClose }) => {
  const [student, setStudent] = useState(null);

  const deleteStudent = async (id) => {
    try {
      const {
        data: { result },
      } = await studentAPI.deleteStudent(id);
      if (result) {
        alert(" ");
        modalClose();
      }
    } catch {}
  };

  const handleDelete = (id) => {
    HandleConfirm("?", () => deleteStudent(id), null);
  };

  useEffect(() => {
    setStudent(studentData);
  }, []);

  return (
    <StudentDetailPresenter
      student={student}
      setModalOpen={setModalOpen}
      setModalID={setModalID}
      setModalData={setModalData}
      handleDelete={handleDelete}
    />
  );
};

export default StudentDetailContainer;
