import DailyTablePresenter from './DailyTablePresenter';
import React, { useState, useEffect, useRef } from 'react';
import { tableAPI } from 'api';

const DailyTableContainer = () => {
  const [dayIndex, setDayIndex] = useState(0);
  const [openShare, setOpenShare] = useState(false);
  const [table, setTable] = useState([]);
  const [active, setActive] = useState(false);
  const [dailyTable, setDailyTable] = useState([]);
  const [loading, setLoading] = useState(false);
  const [CSVHeaders, setCSVHeaders] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const getTable = async () => {
    try {
      setLoading(true);
      const {
        data: { table: tableData }
      } = await tableAPI.readTable();
      setTable(JSON.parse(JSON.stringify(tableData)));
      setActive(tableData.active);
    } catch {
    } finally {
      setLoading(false);
    }
  };

  const getDailyTable = () => {
    const dt = [];
    table &&
      table.group &&
      table.group.forEach((row) => {
        dt.push({ type: 0, teacher: row.teacher, subject: row.teacher.subject, lecture: row.lecture[dayIndex] });
      });
    table &&
      table.personal &&
      table.personal.forEach((row) => {
        dt.push({ type: 1, teacher: row.teacher, subject: row.teacher.subject, lecture: row.lecture[dayIndex] });
      });

    setDailyTable(dt);
  };

  const getCSVHeaders = () => {
    const csvHeaders = [
      { label: '', key: 'type' },
      { label: '', key: 'teacher' },
      { label: 'A', key: 'lecture.A' },
      { label: 'B', key: 'lecture.B' },
      { label: 'C', key: 'lecture.C' },
      { label: 'D', key: 'lecture.D' },
      { label: 'E', key: 'lecture.E' },
      { label: 'F', key: 'lecture.F' }
    ];

    setCSVHeaders(csvHeaders);
  };

  const getCSVData = () => {
    const data = dailyTable.map((row) => {
      const lecture = {};
      for (const time in row.lecture) {
        const cell = row.lecture[time];
        lecture[time] = row.type ? cell.student && cell.student.name : cell.name;
      }
      return {
        type: row.type ? '' : '',
        teacher: `${row.teacher.name} (${row.teacher.subject})`,
        lecture
      };
    });

    setCSVData(data);
  };

  const handleChangeDayIndex = ({ target: { selectedOptions } }) => {
    const dayIdx = selectedOptions[0].value;
    setDayIndex(dayIdx);
  };

  useEffect(() => {
    let isMounted = true;
    if (isMounted) {
      getTable();
      getCSVHeaders();
    }
    return () => (isMounted = false);
  }, []);

  useEffect(() => {
    getDailyTable();
  }, [table]);

  useEffect(() => {
    getDailyTable();
  }, [dayIndex]);

  useEffect(() => {
    getCSVData();
  }, [dailyTable]);

  return (
    <DailyTablePresenter
      openShare={openShare}
      setOpenShare={setOpenShare}
      dayIndex={dayIndex}
      handleChangeDayIndex={handleChangeDayIndex}
      active={active}
      dailyTable={dailyTable}
      loading={loading}
      CSVHeaders={CSVHeaders}
      CSVData={CSVData}
    />
  );
};

export default DailyTableContainer;
