import { createGlobalStyle } from 'styled-components';
import reset from 'styled-reset';
import checkImg from 'assets/checkImg.png';

const GlobalStyles = createGlobalStyle`
    ${reset};
    @import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');        

    @import "~slick-carousel/slick/slick.css"; 
    @import "~slick-carousel/slick/slick-theme.css";

    body {
        font-family: 'NanumSquare', sans-serif;
        background-color: #EEF5F9;  
        // overflow: hidden;
      }

    // checkbox

  .container {
    max-width: 640px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 13px;
    display: flex;  
  }
    
  ul.ks-cboxtags {
    list-style: none;
    // padding: 1px 2px;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 5px;
  }

  ul.ks-cboxtags li{
    display: inline;
  }
  
  ul.ks-cboxtags li label{
    display: inline-block;
    background-color: rgba(255, 255, 255, .9);
    border: 2px solid rgba(139, 139, 139, .3);
    color: #adadad;
    border-radius: 25px;
    white-space: nowrap;
    margin: 3px 0px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: all .2s;
  }
  
  ul.ks-cboxtags li label {
    padding: 8px 12px;
    cursor: pointer;
  }
  
  ul.ks-cboxtags li label::before {
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 12px;
    padding: 2px 6px 2px 2px;
    content: "\f067";
    transition: transform .3s ease-in-out;
  }
  
  ul.ks-cboxtags li input[type="checkbox"]:checked + label::before {
    content: "\f00c";
    transform: rotate(-360deg);
    transition: transform .3s ease-in-out;
  }
  
  ul.ks-cboxtags li input[type="checkbox"]:checked + label {
    border: 2px solid #1bdbf8;
    background-color: #12bbd4;
    color: #fff;
    transition: all .2s;
  }
  
  ul.ks-cboxtags li input[type="checkbox"] {
    display: absolute;
  }

  ul.ks-cboxtags li input[type="checkbox"] {
    position: absolute;
    opacity: 0;
  }

  ul.ks-cboxtags li input[type="checkbox"]:focus + label {
    border: 2px solid #e9a1ff;
  }

  input[type="checkbox"]{
    display: none;
  }

  .toggle{
    display: block !important;
  }
  
  input[type="checkbox"] + label span {
    display: inline-block;
    width: 19px;
    height: 19px;
    margin:-2px 0px 0 0;
    vertical-align:middle;
    background:url(${checkImg}) left top no-repeat;
    cursor:pointer;
  } 

  input[type="checkbox"]:checked + label span {
    background:url(${checkImg}) -19px top no-repeat;
  }

  .cboxlabel{
    margin-right: 10px;
    display: inline-block;
    margin-bottom: 10px;
  }

  .headerLogo{
    height: 60px;
    width: 60px;
  }

  // header  search 
  .search-box{
    padding: 10px;
    position: absolute;
    right: 0;
    margin-right: 50px;
    height: 30px;
    background-color: #75BDF8;
    transition: 0.4s;
    width:30px;
  }

  .search-box:hover{
    width: 282px;
  }

  .search-btn{
    text-decoration: none;
    float: right;
    width: 30px;
    height: 30px;
    background-color: #75BDF8;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fafafa;
  }

  .search-box:hover > .search-btn{
    background-color: #fafafa;
    color: #75BDF8;
  }

  .search-txt{
    display: flex;
    padding: 0;
    width: 0px;
    border:none;
    background: none;
    outline: none;
    float: left;
    font-size: 1rem;
    line-height: 30px;
    transition: .4s;
    color: #fafafa;
  }

  .search-txt::placeholder{
    font-family: 'NanumSquare', sans-serif;
    color: #ECECEC; 
  }

  .search-box:hover > .search-txt{
    width: 240px;
    padding: 0 6px;
  }

  // ,  hover ,     
  .show{
    display: flex;
    gap: 15px;
  }
  .hidden{
    display: none;
  }

  //           
  .tooltip{
    position: relative;
  }
  
  //    
  .tooltip[data-tooltip]:not([data-tooltip=""])::before {
    content: attr(data-tooltip);
    position: absolute;
    font-size: 13px;
    background-color: #4a4a4a;
    color: #fff;
    padding: 8px 15px;
    border-radius: 10px;
    width: max-content;
    left: 25%;
    bottom: 120%;
    opacity: 0.8;
    visibility: hidden;
  }
  
  //     ,     
  .tooltip[data-tooltip]:not([data-tooltip=""])::after {
    content: '';
    border-width: 6px;
    border-style: solid;
    border-color: transparent;
    border-top-color:  #4a4a4a;
    width: 0;
    height: 0;
    display: inline-block;
    position: absolute;
    left: 50%;
    bottom: 70%;
    transform: translate(-50%, 0);
    opacity: 0.8;
    visibility: hidden;
  }

  .tooltip:hover[data-tooltip]:not([data-tooltip=""])::before
  , .tooltip:hover[data-tooltip]:not([data-tooltip=""])::after {
      visibility: visible;
  }


    
`;

export default GlobalStyles;
