import styled from 'styled-components';
import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import { CSVLink } from 'react-csv';

const onDownloadBtn = () => {
  domtoimage
    .toBlob(document.querySelector('.table'), { bgcolor: 'white' })
    .then((blob) => {
      // TODO: /     ex) .png
      saveAs(blob, 'table.png');
    })
    .catch((e) => {
      console.log(e);
    });
};

const SideButtons = ({ openShare, CSVHeaders, CSVData, day }) => {
  return (
    <Side>
      <PngButton openShare={openShare} onClick={onDownloadBtn}>
        <i className="fas fa-file-image"></i> png{' '}
      </PngButton>
      <XslxButton openShare={openShare}>
        <StyledCSVLink data={CSVData} headers={CSVHeaders} filename={`${day} .csv`}>
          <i className="fas fa-file-excel"></i> xlsx
        </StyledCSVLink>
      </XslxButton>
    </Side>
  );
};

const DailyTablePresenter = ({
  openShare,
  setOpenShare,
  dayIndex,
  handleChangeDayIndex,
  active,
  dailyTable,
  loading,
  CSVHeaders,
  CSVData
}) => {
  const days = ['', '', '', '', '', '', ''];
  const times = ['A', 'B', 'C', 'D', 'E', 'F'];
  return (
    <>
      <Head>
        <Select value={dayIndex} onChange={handleChangeDayIndex}>
          {days.map((day, index) => (
            <option value={index}> {day} </option>
          ))}
        </Select>
        <ShareButton
          onClick={() => {
            setOpenShare(!openShare);
          }}
        >
          <i className="fas fa-file-download"></i>&nbsp; 
        </ShareButton>
        <SideButtons openShare={openShare} CSVHeaders={CSVHeaders} CSVData={CSVData} day={`${days[dayIndex]}`} />
      </Head>

      {active ? (
        <Body>
          <TableSection>
            <Table>
              <LectureTypeHead>  </LectureTypeHead>
              <TeacherNameHead>  </TeacherNameHead>
              {times.map((time, index) => (
                <TableTime col={index + 3}> {time} </TableTime>
              ))}
              {dailyTable &&
                dailyTable.length > 0 &&
                dailyTable.map((row, rowIdx) => (
                  <>
                    <LectureType index={rowIdx}> {row.type === 0 ? '' : ''} </LectureType>
                    <TeacherName index={rowIdx}>
                      {row.teacher && row.teacher.name} ({row.teacher && row.teacher.subject})
                    </TeacherName>
                    {times.map((time, timeIdx) => (
                      <TableContent row={rowIdx + 2} col={timeIdx + 3}>
                        {row.type === 0
                          ? row.lecture[time] && row.lecture[time] && row.lecture[time].name
                          : row.lecture[time] && row.lecture[time].student && row.lecture[time].student.name}
                      </TableContent>
                    ))}
                  </>
                ))}
            </Table>
          </TableSection>
        </Body>
      ) : (
        <Body>
          <TableSection>
            <NotSelectedMsg>  .</NotSelectedMsg>
          </TableSection>
        </Body>
      )}
    </>
  );
};

const Head = styled.div`
  display: flex;
  position: sticky;
  gap: 30px;
  top: 0;
  min-height: 32px;
  align-items: center;
  background-color: white;
  padding: 20px 40px;
  font-size: 15px;
  // FIXME:   
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.2);
`;

const Body = styled.div`
  height: 72vmin;
`;

const Select = styled.select`
  border: none;
  padding: 5px 10px;
  font: inherit;
  font-size: 13px;
  width: 100%;
  flex-basis: 10%;
`;

const Table = styled.div`
  display: grid;
  position: absolute;
  margin: 10px 10px;
  width: 100%;
  background-color: white;
  text-align: center;
  align-items: center;
  font-size: 14px;
  color: gray;
  border-top: 1px solid lightgrey;
  border-left: 1px solid lightgrey;
  grid-template-columns: repeat(8, 1fr);
`;

const TableSection = styled.div`
  padding: 30px 30px;
  height: 75vmin;
  width: 1000px;
  position: relative;
  box-sizing: border-box;
`;

const GridBorder = styled.div`
  padding: 5px;
  border-bottom: 1px solid lightgrey;
  border-right: 1px solid lightgrey;
  min-height: 14px;
`;

const LectureTypeHead = styled(GridBorder)`
  grid-row: 1/2;
  grid-column: 1/2;
  border-bottom: 5px double lightgrey;
`;

const TeacherNameHead = styled(GridBorder)`
  grid-row: 1/2;
  grid-column: 2/3;
  border-bottom: 5px double lightgrey;
  border-right: 5px double lightgrey;
`;

const LectureType = styled(GridBorder)`
  grid-row: ${(props) => props.index + 2};
  grid-column: 1/2;
  width: 125px;
`;

const TeacherName = styled(GridBorder)`
  grid-row: ${(props) => props.index + 2};
  grid-column: 2/3;
  width: 125px;
  border-right: 5px double lightgrey;
`;

const TableTime = styled(GridBorder)`
  grid-row: 1/2;
  grid-column: ${(props) => props.col};
  border-bottom: 5px double lightgrey;
`;

const TableContent = styled(GridBorder)`
  grid-row: ${(props) => props.row};
  grid-column: ${(props) => props.col};
`;

const Side = styled.div`
  display: flex;
  margin-left: -30px;
  gap: 5px;
`;

const Button = styled.button`
  font-size: 13px;
  border-radius: 14px;
  border: none;
  padding: 8px 15px;
  font-weight: 800;
  cursor: pointer;
`;

const ShareButton = styled(Button)`
  background-color: #fdf5e7;
  color: #e6cc9c;
  z-index: 10;
  transition: all 0.3s;
  &:hover {
    transform: scale(1.1);
  }
`;

// FIXME: .    Dom 
const PngButton = styled(Button)`
  background-color: #ffc57e;
  color: #fafafa;
  transition: transform 0.5s ease;
  transform: ${(props) => (props.openShare ? 'translateX(20px)' : 'translateX(-70px)')};
`;

const XslxButton = styled(Button)`
  background-color: #ffc57e;
  color: #fafafa;
  transition: transform 0.5s ease;
  transform: ${(props) => (props.openShare ? 'translateX(25px)' : 'translateX(-150px)')};
`;

const StyledCSVLink = styled(CSVLink)`
  text-decoration: none;
  color: white;
`;

const NotSelectedMsg = styled.div`
  color: gray;
  font-size: 15px;
`;

export default DailyTablePresenter;
