import styled from 'styled-components';

const GroupModifyPresenter = ({
  group,
  subject,
  selectedTeacher,
  selectedStudents,
  students,
  teachers,
  days,
  times,
  handleChangeOfficeHours,
  handleChangeTimes,
  officeHours,
  handleSelectStudent,
  handleChangeTeacher,
  handleRemoveStudent,
  handleChangeSubject,
  handleSubmit,
  inputName,
  validate,
  errors
}) => {
  return (
    <Main>
      <ModalLeft>
        <ModalSection>
          <ModalTitle>  </ModalTitle>
          <ModalInput
            ref={inputName}
            type="text"
            name="name"
            placeholder=" "
            defaultValue={(group && group.name) || ''}
          />
        </ModalSection>

        <ModalSection>
          <ModalTitle>  </ModalTitle>
          <ModalSelectContainer>
            <ModalSelect onChange={handleChangeSubject} value={subject && subject}>
              <option value=""> .</option>
              <option value="">  </option>
              <option value="">  </option>
              <option value="">  </option>
              <option value="">  </option>
              <option value="">  </option>
            </ModalSelect>

            <ModalSelect
              onChange={handleChangeTeacher}
              value={
                (teachers &&
                  selectedTeacher &&
                  teachers.findIndex((teacher) => String(teacher._id) === String(selectedTeacher._id))) ||
                ''
              }
              disabled={subject ? false : true}
            >
              <option value=""> .</option>
              {teachers &&
                teachers.length > 0 &&
                teachers.map((teacher, index) => (
                  <ModalSelectOption value={index} isAvailable={teacher.isAvailable} disabled={!teacher.isAvailable}>
                    {teacher.name}
                  </ModalSelectOption>
                ))}
            </ModalSelect>
          </ModalSelectContainer>
        </ModalSection>

        <ModalSection>
          <ModalStudentTitle>
            <ModalTitle>  </ModalTitle>
          </ModalStudentTitle>

          <ModalSelect onChange={handleSelectStudent}>
            <option value=""> .</option>
            {students &&
              students.length > 0 &&
              students.map((student, index) => (
                <ModalSelectOption value={index} isAvailable={student.isAvailable} disabled={!student.isAvailable}>
                  {student.name}
                </ModalSelectOption>
              ))}
          </ModalSelect>

          <SelectedSection>
            {selectedStudents &&
              selectedStudents.map((student, index) => (
                <SelectedStudent
                  key={index}
                  id={student._id}
                  isAvailable={student.isAvailable}
                  onClick={handleRemoveStudent}
                >
                  {student.name}
                </SelectedStudent>
              ))}
          </SelectedSection>
        </ModalSection>
      </ModalLeft>

      <ModalRight>
        <ModalSection>
          <ModalTitle> /</ModalTitle>
          {days.map((day, index) => (
            <span key={`GroupModifyDayCheckbox${index}`}>
              <input
                type="checkbox"
                id={`day${index}`}
                name="officeHours"
                value={day}
                onChange={handleChangeOfficeHours}
                checked={
                  officeHours &&
                  officeHours.length > 0 &&
                  officeHours.findIndex((officeHour) => officeHour.day === day) > -1
                }
              />
              <label htmlFor={`day${index}`}>
                <span></span> <div className="cboxlabel"> {day} </div>{' '}
              </label>
            </span>
          ))}

          {officeHours &&
            officeHours.length > 0 &&
            officeHours.map((officeHour, oIndex) => (
              <div className="container" key={`GroupModifyOfficeHour${oIndex}`}>
                <ul className="ks-cboxtags">
                  {times.map((time, tIndex) => (
                    <li key={`GroupModifyTime${tIndex}`}>
                      <input
                        type="checkbox"
                        id={`day${oIndex}time${tIndex}`}
                        name="times"
                        value={`${officeHour.day}${time}`}
                        onChange={handleChangeTimes}
                        checked={officeHour.times.includes(time)}
                      />
                      <label htmlFor={`day${oIndex}time${tIndex}`}>
                        {officeHour.day}
                        {time}
                      </label>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
        </ModalSection>

        {Object.keys(errors).length === 0
          ? null
          : Object.keys(errors).map((key, index) => (
              <ModalFormError>
                {' '}
                <i class="fas fa-exclamation-circle"></i> &nbsp; {errors[key]}{' '}
              </ModalFormError>
            ))}

        <SubmitButton onClick={validate}>  </SubmitButton>
      </ModalRight>
    </Main>
  );
};

const SubmitButton = styled.button`
  display: block;
  background-color: #eef5f9;
  color: black;
  height: 58px;
  border-radius: 4px;
  margin-top: 7px;
  font-size: 15px;
  line-height: 58px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: 0;
  &:hover {
    background-color: #75bdf8;
    color: #fafafa;
    transition-duration: 0.3s;
  }
`;

const ModalSelectContainer = styled.div`
  display: flex;
  gap: 20px;
`;

const ModalStudentTitle = styled.div`
  display: flex;
  align-items: center;
`;

const ModalTitle = styled.div`
  font-size: 14px;
  line-height: 28px;
  font-weight: 800;
  margin-bottom: 10px;
`;

const ModalInput = styled.input`
  flex-basis: 70%;
  width: 100%;
  height: 40px;
  padding-left: 17px;
  border: none;
  border-bottom: 1px solid #efefef;
  margin: 7px 0px;
`;

const ModalFormError = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #ff4e5d;
  color: #fff;
  padding: 8px;
  font-size: 13px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-top: 10px;
`;

const ModalSelect = styled.select`
  border: none;
  padding: 5px 10px;
  font: inherit;
  width: 100%;
  font-size: 12px;
  flex-basis: 50%;
`;

const ModalSelectOption = styled.option`
  color: ${(props) => (props.isAvailable ? 'black' : '#fe294f')};
`;

const Main = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 35px 35px 25px;
`;

const ModalLeft = styled.div`
  padding: 20px 40px;
  font-size: 13px;
`;

const ModalRight = styled.div`
  padding: 20px 40px;
  font-size: 13px;
`;

const ModalSection = styled.div`
  margin-bottom: 32px;
`;

const ButtonGroup = styled.div`
  display: inline-block;
  font-size: 12px;
  background-color: rgba(255, 255, 255, 0.9);
  border: 2px solid rgba(139, 139, 139, 0.3);
  color: #adadad;
  border-radius: 25px;
  white-space: nowrap;
  margin: 3px 0px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: all 0.2s;
  padding: 8px 8px;
  cursor: pointer;
`;

const AddStudentBtn = styled.div`
  margin-left: 5px;
  font-size: 14px;
  color: #999;
  font-weight: 700;
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 10px;
`;

const SelectedStudent = styled.div`
  border: 2px solid ${(props) => (props.isAvailable ? 'rgba(139, 139, 139, 0.3)' : '#fe294f')};
  color: ${(props) => (props.isAvailable ? '#adadad;' : '#fe294f')};
  width: fit-content;
  text-align: center;
  border-radius: 25px;
  white-space: nowrap;
  margin: 3px 0px;
  padding: 8px 12px;
  cursor: pointer;
  &:hover {
    opacity: 0.5;
    transition: opacity 0.3s ease;
  }
  &:hover::before {
    font-size: 13px;
  }
`;
const SelectedSection = styled.div`
  display: flex;
  flex-wrap: wrap;

  gap: 10px;
  margin-top: 10px;
`;

const RemoveStudentBtn = styled(AddStudentBtn)`
  margin-bottom: 0px;
  margin-left: 0px;
`;

export default GroupModifyPresenter;
