import Modal from "Components/Modal";
import { useState } from "react";
import styled from "styled-components";

const TeacherHead = styled.div`
  display: flex;
  position: sticky;
  top: 0;
  min-height: 32px;
  align-items: center; 
  background-color: white;
  padding: 20px 40px;
  font-size: 15px;
  // FIXME:   
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.2);
`;

const TeacherSection = styled.div`
  overflow: scroll;
  height: 72vmin;
`

const TeacherBody = styled.div`
  line-height: 30px;
  padding: 10px 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  color: gray;
  &:hover {
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
  }
`;

const TeacherName = styled.div`
  flex-basis: 25%;
`;

const TeacherPhone = styled.div`
  flex-basis: 25%;
`;
const TeacherResidence = styled.div`
  flex-basis: 25%;
`;

const TeacherSubject = styled.div`
  flex-basis: 25%;
`;

const TeacherManage = styled.div`
  flex-basis: 25%;
`;
const Buttons = styled.div`
  display: flex;
  gap: 10px;
`;
const NewButton = styled.div`
  right: 1.4%;
  background-color: #59bffd;
  color: #ffffff;
  font-size: 18px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transition: all 0.3s;
  &:hover{
    transform: scale(1.1);
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`;

const EditButton = styled.div`
  color: #79797b;
  background-color: #fafafa;
  width: 30px;
  height: 30px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;

  &:hover {
    transition-duration: 0.3s;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`;

const DeleteButton = styled.div`
  color: #dd6868;
  background-color: #fdf6f7;
  width: 30px;
  height: 30px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;

  &:hover {
    transition-duration: 0.3s;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`;
function Teacher({ setModalOpen, setModalID, setModalData, teacher, handleDelete, keyProp }) {
  const [hide, setHide] = useState(true);

  return (
    <TeacherBody
      onMouseEnter={() => {
        setHide(false);
      }}
      onMouseLeave={() => {
        setHide(true);
      }}
      onClick={(e) => {
        setModalOpen(true);
        setModalID("TeacherDetail");
        setModalData(teacher);
        e.stopPropagation();
      }}
    >
      <TeacherName> {teacher.name} </TeacherName>
      <TeacherSubject> {teacher.subject} </TeacherSubject>
      <TeacherPhone> {teacher.phone}</TeacherPhone>
      <TeacherResidence>
        {teacher.officeHours.map((hour, index) => (
          <span key={`${keyProp}officeHour${index}`}>{hour.day} </span>
        ))}
      </TeacherResidence>
      <TeacherManage>
        <Buttons className={hide ? "hidden" : "show"}>
          <EditButton
            onClick={(e) => {
              setModalOpen(true);
              setModalID("TeacherModify");
              setModalData(teacher);
              e.stopPropagation();
            }}
          >
            <i className="fas fa-pencil-alt"></i>
          </EditButton>
          <DeleteButton
            onClick={(e) => {
              handleDelete(teacher._id);
              e.stopPropagation();
            }}
          >
            <i className="fas fa-trash"></i>
          </DeleteButton>
        </Buttons>
      </TeacherManage>
    </TeacherBody>
  );
}
const TeacherPresenter = ({
  modalOpen,
  setModalOpen,
  modalID,
  setModalID,
  modalData,
  setModalData,
  teachers,
  modalClose,
  handleDelete,
}) => {
  return (
    <>
      <TeacherHead>
        <TeacherName>  </TeacherName>
        <TeacherSubject>  </TeacherSubject>
        <TeacherPhone>  </TeacherPhone>
        <TeacherResidence>  </TeacherResidence>
        <TeacherManage> {/* / */} </TeacherManage>
      </TeacherHead>

      <TeacherSection>

      {teachers &&
        teachers.map((teacher, index) => (
          <Teacher
            setModalOpen={setModalOpen}
            setModalID={setModalID}
            setModalData={setModalData}
            teacher={teacher}
            handleDelete={handleDelete}
            key={`teacher${index}`}
            keyProp={`teacher${index}`}
          ></Teacher>
        ))}
      <NewButton
        onClick={(e) => {
          setModalOpen(true);
          setModalID("TeacherRegister");
          e.stopPropagation();
        }}
      >
        <i className="fas fa-user-plus"></i>
      </NewButton>
      <Modal
        modalOpen={modalOpen}
        setModalOpen={setModalOpen}
        modalID={modalID}
        setModalID={setModalID}
        modalData={modalData}
        setModalData={setModalData}
        modalClose={modalClose}
      />
      </TeacherSection>

    </>
  );
};

export default TeacherPresenter;
