import Modal from "Components/Modal";
import { useState } from "react";
import styled from "styled-components";

const GroupHead = styled.div`
  display: flex;
  position: sticky;
  top: 0;
  min-height: 32px;
  align-items: center; 
  background-color: white;
  padding: 20px 40px;
  font-size: 15px;
  // FIXME:   
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.2);
`;

const GroupSection = styled.div`
  overflow: scroll;
  height: 72vmin;
`

const GroupBody = styled.div`
  line-height: 30px;
  padding: 10px 40px;
  flex-basis: 90%;
  display: flex;
  align-items: center;
  font-size: 14px;
  color: gray;
  &:hover {
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
  }
`;

const GroupName = styled.div`
  flex-basis: 20%;
`

const GroupSubject = styled.div`
  flex-basis: 10%;
`

const GroupTeacher = styled.div`
  flex-basis: 10%;
`

const GroupTime = styled.div`
  flex-basis: 20%;
`

const GroupStudent = styled.div`
  flex-basis: 20%;
`

const GroupManage = styled.div`
  flex-basis: 10%;
  margin-left: 30px;
`;


const Buttons = styled.div`
`;

const ButtonStyle = styled.div`
  width: 30px;
  height: 30px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  &:hover {
    transition-duration: 0.3s;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`
const NewButton = styled(ButtonStyle)`
  right: 1.4%;
  background-color: #59bffd;
  color: #ffffff;
  font-size: 18px;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  transition: all 0.3s;
  &:hover{
    transform: scale(1.1);
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`;

const EditButton = styled(ButtonStyle)`
  color: #79797b;
  background-color: #fafafa;
`;

const DeleteButton = styled(ButtonStyle)`
  color: #dd6868;
  background-color: #fdf6f7;
`;

const Group = ({setModalOpen, setModalID, setModalData, handleDelete, group}) => { 
  const [hide, setHide] = useState(true)
  return (
    <GroupBody
      onMouseEnter={()=>{setHide(false)}}
      onMouseLeave={()=>{setHide(true)}}>
        <GroupName> { group.name && group.name } </GroupName>
        <GroupSubject> { group.teacher.subject && group.teacher.subject }</GroupSubject>
        <GroupTeacher> { group.teacher.name && group.teacher.name } </GroupTeacher> 
        <GroupTime>
          { group.officeHours.map((hour,index)=>(
            <> {hour.day && hour.day}({hour.times && hour.times})</>
          ))}
        </GroupTime>
        <GroupStudent> 
          { group.students && group.students.map((stu, index)=>(
            <> { stu.name && stu.name } </>
          )) }
        </GroupStudent>
        <GroupManage> 
          <Buttons className={hide? "hidden" : "show"}>  
              <EditButton onClick={(e)=>{
                setModalOpen(true);
                setModalID('GroupModify');
                setModalData(group);
                e.stopPropagation();
              }}>
                <i className="fas fa-pencil-alt"></i>
              </EditButton>
              <DeleteButton onClick={(e)=>{ handleDelete(group._id); e.stopPropagation()}}>
                  <i className="fas fa-trash"></i>
              </DeleteButton>
          </Buttons>
        </GroupManage>
    </GroupBody>
  )
}

const GroupPresenter = ({
  modalOpen,
  setModalOpen,
  modalID,
  setModalID,
  modalData,
  setModalData,
  modalClose,
  handleDelete,
  groups
}) => {
    return (
        <>
          <GroupHead>
              <GroupName>  </GroupName>
              <GroupSubject>  </GroupSubject>
              <GroupTeacher>  </GroupTeacher>
              <GroupTime>   </GroupTime>
              <GroupStudent>  </GroupStudent>
              <GroupManage> </GroupManage>
          </GroupHead>
          <GroupSection>
            {
              groups && groups.map((group,index)=>(
                <Group group={group}
                setModalOpen={setModalOpen}
                setModalID={setModalID}
                setModalData={setModalData}
                handleDelete={handleDelete}>
                </Group> 
              ))
            }
              <NewButton onClick={ (e)=>{ setModalOpen(true); setModalID("GroupRegister"); e.stopPropagation(); } }>
                <i className="fas fa-user-plus"></i>
              </NewButton>
              <Modal
                modalOpen={modalOpen}
                setModalOpen={setModalOpen}
                modalID={modalID}
                setModalID={setModalID}
                modalData={modalData}
                setModalData={setModalData}
                modalClose={modalClose}
              />
          </GroupSection>
      </>
    )
}

export default GroupPresenter;
