import React, { useState, useEffect, useRef } from "react";
import { teacherAPI } from "api";
import TeacherModifyPresenter from "./TeacherModifyPresenter";
import HandleConfirm from "Components/HandleConfirm";

const TeacherModifyContainer = ({ modalClose, teacherData }) => {
  //        
  const days = ["", "", "", "", "", "", ""];
  const times = ["A", "B", "C", "D", "E", "F"];

  const [teacher, setTeacher] = useState(null);
  const [subject, setSubject] = useState("");
  const [officeHours, setOfficeHours] = useState([]);
  const [isModify, setIsModify] = useState(false);
  const [errors, setErrors] = useState(new Object());

  useEffect(() => {
    setTeacher(teacherData);
    teacherData && setSubject(teacherData.subject);
    teacherData && setOfficeHours(teacherData.officeHours);
    teacherData && setIsModify(true);
  }, []);

  const inputName = useRef();
  const inputPhone = useRef();

  const handleChangeOfficeHours = ({ currentTarget: target }) => {
    const { value, checked } = target;
    const index = officeHours.findIndex((officeHour) => officeHour.day === value);
    if (checked && index < 0) officeHours.push({ day: value, times });
    if (!checked && index > -1) officeHours.splice(index, 1);
    officeHours.sort((a, b) => (days.indexOf(a.day) >= days.indexOf(b.day) ? 1 : -1));
    setOfficeHours(JSON.parse(JSON.stringify(officeHours)));
  };

  const handleChangeTimes = (e) => {
    const target = e.currentTarget;
    const { value, checked } = target;
    const [day, time] = value.split("");
    const dayIndex = officeHours.findIndex((officeHour) => officeHour.day === day);
    if (dayIndex > -1) {
      const timeIndex = officeHours[dayIndex].times.findIndex((t) => t === time);
      if (checked && timeIndex < 0) officeHours[dayIndex].times.push(time);
      if (!checked && timeIndex > -1) officeHours[dayIndex].times.splice(timeIndex, 1);
      officeHours[dayIndex].times.sort((a, b) => (times.indexOf(a) >= times.indexOf(b) ? 1 : -1));
      if (officeHours[dayIndex].times.length < 1) {
        e.stopPropagation();
        officeHours.splice(dayIndex, 1);
      }
    }
    setOfficeHours(JSON.parse(JSON.stringify(officeHours)));
  };

  const handleChangeSubject = ({ target: { selectedOptions } }) => {
    const subject = selectedOptions[0].value;
    setSubject(subject);
  };

  const validate = () => {
    const temp_errors = {};
    const regPhone = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;
    const newTeacher = {
      name: inputName.current.value,
      phone: inputPhone.current.value,
      subject,
      officeHours,
    };

    for (var key in newTeacher) {
      // Input  
      if (newTeacher[key].length === 0 || newTeacher[key] === undefined) {
        if (key === 'name') {
          temp_errors[key] = '  .';
        } else if(key === "phone"){
          temp_errors[key]= '  .';
        }else if (key === 'subject') {
          temp_errors[key] = '  .';
        } else if (key === 'officeHours') {
          temp_errors[key] = ' .';
        }
      }


      //   (A~F)    
      if (key === 'officeHours') {
        for (var i = 0; i < newTeacher[key].length; i++) {
          if (!newTeacher[key][i].times) {
            temp_errors[key] = newTeacher[key][i].day + '  .';
          }
        }
      }

      //   
      if (key === 'phone' && newTeacher[key].length != 0 && regPhone.test(newTeacher[key]) === false) {
        temp_errors[key] = '    .';
      }
    }

    //   handleSubmit
    if (Object.keys(temp_errors).length === 0 && temp_errors.constructor === Object) {
      handleSubmit(newTeacher);
    } else {
      setErrors({ ...temp_errors });
    }
  };

  const handleSubmit = async (newTeacher) => {
    if (isModify) newTeacher._id = teacher._id;
    HandleConfirm("?", () => (isModify ? updateTeacher(newTeacher) : createTeacher(newTeacher)), null);
  };

  const createTeacher = async (teacher) => {
    try {
      const {
        data: { result },
      } = await teacherAPI.createTeacher(teacher);
      if (result) {
        alert(" ");
        modalClose();
      }
    } catch {}
  };

  const updateTeacher = async (teacher) => {
    try {
      const {
        data: { result },
      } = await teacherAPI.updateTeacher(teacher);
      if (result) {
        alert(" ");
        modalClose();
      }
    } catch {}
  };

  return (
    <TeacherModifyPresenter
      handleChangeOfficeHours={handleChangeOfficeHours}
      handleChangeTimes={handleChangeTimes}
      handleChangeSubject={handleChangeSubject}
      handleSubmit={handleSubmit}
      teacher={teacher}
      subject={subject}
      days={days}
      times={times}
      officeHours={officeHours}
      inputName={inputName}
      inputPhone={inputPhone}
      errors={errors}
      validate={validate}
    />
  );
};

export default TeacherModifyContainer;
