import React from 'react';
import styled from 'styled-components';
import { Link, withRouter } from 'react-router-dom';

const MenuList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
`;

const MenuItem = styled(Link)`
  text-decoration: none;
  margin-bottom: 40px;
  cursor: pointer;
  width: 100%;
  color: #cccccc;
`;

const MenuTitle = styled.span`
  font-size: 15px;
  margin-left: 15px;
  text-align: left;
  color: ${(props) => (props.current ? "gray" : "#cccccc")} !important;
  font-weight: ${(props) => (props.current ? "bold" : "")};
`;

const Menu = ({ location: { pathname } }) => {
  return (
    <>
      <MenuList>
        <MenuItem to="/">
          <MenuTitle current={pathname == "/" ? true : false}> </MenuTitle>
        </MenuItem>
        
        <MenuItem to="/teachers">
          <MenuTitle current={pathname == "/teachers" ? true : false}> </MenuTitle>
        </MenuItem>

        <MenuItem to="/groups">
          <MenuTitle current={pathname == "/groups" ? true : false}> </MenuTitle>
        </MenuItem>

        <MenuItem to="/tables">
          <MenuTitle current={pathname == "/tables" ? true : false}> </MenuTitle>
        </MenuItem>

        <MenuItem to="/tables/daily">
          <MenuTitle current={pathname == "/tables/daily" ? true : false}>   </MenuTitle>
        </MenuItem>

        <MenuItem to="/tables/personal">
          <MenuTitle current={pathname == "/tables/personal" ? true : false}>   </MenuTitle>
        </MenuItem>
      </MenuList>
    </>
  );
};

export default withRouter(Menu);
