import React, { useState, useEffect } from "react";
import StudentPresenter from "./StudentPresenter";
import { studentAPI } from "api";
import HandleConfirm from "Components/HandleConfirm";

const StudentContainer = () => {
  const [students, setStudents] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalID, setModalID] = useState("");
  const [modalData, setModalData] = useState(null);
  const [sortBy, setSortBy] = useState(false);

  const modalClose = () => {
    setModalOpen(false);
    setModalID("");
    setModalData(null);
  };

  const getStudentsData = async () => {
    try {
      const {
        data: { students: _students },
      } = await studentAPI.readAllStudents();
      setStudents(_students);
    } catch {}
  };

  const deleteStudent = async (id) => {
    try {
      setModalID("");
      const {
        data: { result },
      } = await studentAPI.deleteStudent(id);
      if (result) {
        alert(" ");
      }
    } catch {
    } finally {
      setModalID("FOR RERENDERING"); // trick
    }
  };

  
  const handleDelete = (id) => {
    HandleConfirm("?", () => deleteStudent(id), null);
  };

  useEffect(() => {
    getStudentsData();
  }, [modalOpen, modalID]);
  
  return (
    <StudentPresenter
      modalOpen={modalOpen}
      setModalOpen={setModalOpen}
      students={students}
      modalID={modalID}
      setModalID={setModalID}
      modalData={modalData}
      setModalData={setModalData}
      modalClose={modalClose}
      handleDelete={handleDelete}
      sortBy={sortBy}
      setSortBy={setSortBy}
    />
  );
  
};

export default StudentContainer;
