import styled from 'styled-components';
import { Link } from 'react-router-dom';
import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import { CSVLink } from 'react-csv';

const SideButtons = ({ selectPerson, openShare, CSVData, CSVHeaders }) => {
  if (selectPerson.current && !selectPerson.current.value) return <div></div>;

  const name =
    selectPerson.current &&
    selectPerson.current.options &&
    selectPerson.current.options[selectPerson.current.selectedIndex].text;

  return (
    <Side>
      <PngButton
        openShare={openShare}
        onClick={() => {
          onDownloadBtn(name);
        }}
      >
        <i className="fas fa-file-image"></i> png{' '}
      </PngButton>
      <XslxButton openShare={openShare}>
        <StyledCSVLink data={CSVData} headers={CSVHeaders} filename={`${name}.csv`}>
          <i className="fas fa-file-excel"></i> xlsx
        </StyledCSVLink>
      </XslxButton>
    </Side>
  );
};

const onDownloadBtn = (name) => {
  domtoimage
    .toBlob(document.querySelector('.table'), { bgcolor: 'white' })
    .then((blob) => {
      // TODO: /     ex) .png
      saveAs(blob, `${name}.png`);
    })
    .catch((e) => {
      console.log(e);
    });
};

const PersonalTablePresenter = ({
  openShare,
  setOpenShare,
  dayIndex,
  category,
  personId,
  selectPerson,
  students,
  teachers,
  active,
  personalTable,
  handleChangeCategory,
  handleChangePerson,
  CSVData,
  CSVHeaders
}) => {
  const days = ['', '', '', '', '', '', ''];
  const times = ['A', 'B', 'C', 'D', 'E', 'F'];
  const today = new Date();
  const month = today.getMonth() + 1; // 
  const date = today.getDate(); // 

  return (
    <>
      <Head>
        {/* {month} {date} ({days[dayIndex]}) */}
        <Select onChange={handleChangeCategory} value={category} disabled={!active}>
          <option value="" disabled selected>
            {' '}
            {' '}
          </option>
          <option value="students">  </option>
          <option value="teachers">  </option>
        </Select>
        <Select ref={selectPerson} onChange={handleChangePerson} value={personId} disabled={!active || !category}>
          <option value="" disabled selected>
            {' '}
            {' '}
          </option>

          {category === 'students'
            ? students &&
              students.length > 0 &&
              students.map((student) => <option value={student._id}>{student.name}</option>)
            : teachers &&
              teachers.length > 0 &&
              teachers.map((teacher) => <option value={teacher._id}>{teacher.name}</option>)}
        </Select>
          
        <ShareButton
          onClick={() => {
            setOpenShare(!openShare);
          }}
        >
          <i className="fas fa-file-download"></i>&nbsp; 
        </ShareButton>
        <SideButtons selectPerson={selectPerson} openShare={openShare} CSVData={CSVData} CSVHeaders={CSVHeaders} />
      </Head>
      {active ? (
        category && personId ? (
          <Body>
            <TableSection>
              <Table className="table">
                <Category>  </Category>
                {days.map((day, index) => (
                  <TableDay col={index + 2}>{day}</TableDay>
                ))}

                {times.map((time, index) => (
                  <TableTime row={index + 2}> {time} </TableTime>
                ))}

                {personalTable &&
                  personalTable.map((day, dayIdx) =>
                    times.map((time, timeIdx) => (
                      <TableContent row={timeIdx + 2} col={dayIdx + 2}>
                        {day[time] && day[time].type === 0 && ''}
                        {category === 'students'
                          ? day[time] && `${day[time].subject} (${day[time].name})`
                          : day[time] && day[time].name}
                      </TableContent>
                    ))
                  )}

                {/* //   personalTable[time] &&
              //   `${personalTable[time].subject} (${personalTable[time].name})`
              // : personalTable && personalTable[time] && personalTable[time].name} */}
              </Table>
            </TableSection>
          </Body>
        ) : (
          <Body>
            <TableSection>
              <NotSelectedMsg> .</NotSelectedMsg>
            </TableSection>
          </Body>
        )
      ) : (
        <Body>
          <TableSection>
            <NotSelectedMsg>  .</NotSelectedMsg>
          </TableSection>
        </Body>
      )}
    </>
  );
};

const Head = styled.div`
  display: flex;
  position: sticky;
  gap: 30px;
  top: 0;
  min-height: 32px;
  align-items: center;
  background-color: white;
  padding: 20px 40px;
  font-size: 15px;
  // FIXME:   
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.2);
`;

const Body = styled.div`
  height: 72vmin;
`;

const Select = styled.select`
  border: none;
  padding: 5px 10px;
  font: inherit;
  font-size: 13px;
  width: 100%;
  flex-basis: 10%;
`;

const TableSection = styled.div`
  padding: 30px 30px;
  height: 75vmin;
  width: 1000px;
  position: relative;
  box-sizing: border-box;
`;

const Table = styled.div`
  display: grid;
  position: absolute;
  margin: 10px 10px;
  width: 100%;
  background-color: white;
  text-align: center;
  align-items: center;
  font-size: 14px;
  color: gray;
  border-top: 1px solid lightgrey;
  border-left: 1px solid lightgrey;
  grid-template-columns: repeat(8, 1fr);
`;

const GridBorder = styled.div`
  padding: 10px 10px;
  border-bottom: 1px solid lightgrey;
  border-right: 1px solid lightgrey;
  min-height: 14px;
`;

const Category = styled(GridBorder)`
  grid-column: 1;
  grid-row: 1;
  border-bottom: 5px double lightgrey;
  border-right: 5px double lightgrey;
`;

const TableDay = styled(GridBorder)`
  grid-column: ${(props) => props.col};
  grid-row: 1/2;
  border-bottom: 5px double lightgrey;
`;

const TableTime = styled(GridBorder)`
  grid-column: 1/2;
  grid-row: ${(props) => props.row};
  border-right: 5px double lightgrey;
`;

const TableContent = styled(GridBorder)`
  grid-column: ${(props) => props.column};
  grid-row: ${(props) => props.row};
`;

const Side = styled.div`
  display: flex;
  margin-left: -30px;
  gap: 5px;
`;

const Button = styled.button`
  font-size: 13px;
  border-radius: 14px;
  border: none;
  padding: 8px 15px;
  font-weight: 800;
  cursor: pointer;
`;

const ShareButton = styled(Button)`
  background-color: #fdf5e7;
  color: #e6cc9c;
  z-index: 10;
  transition: all 0.3s;
  &:hover {
    transform: scale(1.1);
  }
`;

// FIXME: .    Dom 
const PngButton = styled(Button)`
  background-color: #ffc57e;
  color: #fafafa;
  transition: transform 0.5s ease;
  transform: ${(props) => (props.openShare ? 'translateX(20px)' : 'translateX(-70px)')};
`;

const XslxButton = styled(Button)`
  background-color: #ffc57e;
  color: #fafafa;
  transition: transform 0.5s ease;
  transform: ${(props) => (props.openShare ? 'translateX(25px)' : 'translateX(-150px)')};
`;

const NotSelectedMsg = styled.div`
  color: gray;
  font-size: 15px;
`;

const StyledCSVLink = styled(CSVLink)`
  text-decoration: none;
  color: white;
`;

export default PersonalTablePresenter;
