import Modal from 'Components/Modal';
import { useState } from 'react';
import styled from 'styled-components';

const Student = ({ setModalOpen, setModalID, setModalData, student, handleDelete, keyProp, sortBy }) => {
  const [hide, setHide] = useState(true);

  return (
    <>
      <StudentBody
        onMouseEnter={() => {
          setHide(false);
        }}
        onMouseLeave={() => {
          setHide(true);
        }}
        onClick={(e) => {
          setModalOpen(true);
          setModalID('StudentDetail');
          setModalData(student);
          e.stopPropagation();
        }}
      >
        <StudentName> {student.name}</StudentName>
        <StudentSchool> {student.school} </StudentSchool>
        <StudentGrade> {student && student.grade ? student.grade : " "} </StudentGrade>
        <StudentCategory> {student.category ? '' : ''} </StudentCategory>
        <StudentResidence>
          {student.officeHours.map((hour, index) => (
            <span key={`${keyProp}officeHour${index}`}>{hour.day} </span>
          ))}
        </StudentResidence>
        <StudentSubject>
          {student.subjects.map((subject, index) => (
            <span key={`${keyProp}subject${index}`}>
              {subject.name}({subject.hours}){' '}
            </span>
          ))}
        </StudentSubject>
        <StudentManage>
          <Buttons className={hide ? 'hidden' : 'show'}>
            <EditButton
              onClick={(e) => {
                setModalOpen(true);
                setModalID('StudentModify');
                setModalData(student);
                e.stopPropagation();
              }}
            >
              <i className="fas fa-pencil-alt"></i>
            </EditButton>
            <DeleteButton
              onClick={(e) => {
                handleDelete(student._id);
                e.stopPropagation();
              }}
            >
              <i className="fas fa-trash"></i>
            </DeleteButton>
          </Buttons>
        </StudentManage>
      </StudentBody>
    </>
  );
};

const StudentPresenter = ({
  modalOpen,
  setModalOpen,
  modalID,
  setModalID,
  modalData,
  setModalData,
  students,
  modalClose,
  handleDelete,
  sortBy,
  setSortBy
}) => {
  return (
    <>
      <StudentHead>
        <StudentName
          onClick={() => {
            setSortBy(!sortBy);
          }}
        >
          {' '}
          {' '}
        </StudentName>
        <StudentSchool>  </StudentSchool>
        <StudentGrade>  </StudentGrade>
        <StudentCategory>  </StudentCategory>
        <StudentResidence>  </StudentResidence>
        <StudentSubject>  </StudentSubject>
        <StudentManage> {/* / */} </StudentManage>
      </StudentHead>
      <StudentSection>
        {students &&
          students.map((student, index) => (
            <Student
              setModalOpen={setModalOpen}
              setModalID={setModalID}
              setModalData={setModalData}
              student={student}
              handleDelete={handleDelete}
              key={`student${index}`}
              keyProp={`student${index}`}
            ></Student>
          ))}
        <NewButton
          onClick={(e) => {
            setModalOpen(true);
            setModalID('StudentRegister');
            e.stopPropagation();
          }}
        >
          <i className="fas fa-user-plus"></i>
        </NewButton>
        <Modal
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalID={modalID}
          setModalID={setModalID}
          modalData={modalData}
          setModalData={setModalData}
          modalClose={modalClose}
        />
      </StudentSection>
    </>
  );
};

const StudentHead = styled.div`
  display: flex;
  position: sticky;
  top: 0;
  min-height: 32px;
  align-items: center;
  background-color: white;
  padding: 20px 40px;
  font-size: 15px;
  // FIXME:   
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.2);
`;

const StudentSection = styled.div`
  overflow: scroll;
  height: 72vmin;
`;

const StudentBody = styled.div`
  line-height: 30px;
  padding: 10px 40px;
  flex-basis: 90%;
  display: flex;
  align-items: center;
  font-size: 14px;
  color: gray;
  &:hover {
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
  }
`;

const StudentName = styled.div`
  flex-basis: 10%;
`;

const StudentSchool = styled.div`
  flex-basis: 10%;
`;

const StudentGrade = styled.div`
  flex-basis: 10%;
`;

const StudentResidence = styled.div`
  flex-basis: 20%;
`;

const StudentSubject = styled.div`
  flex-basis: 20%;
`;

const StudentCategory = styled.div`
  flex-basis: 15%;
`;

const StudentManage = styled.div`
  flex-basis: 10%;
  margin-left: 30px;
`;

const Buttons = styled.div``;

const ButtonStyle = styled.div`
  width: 30px;
  height: 30px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  &:hover {
    transition-duration: 0.3s;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`;

const NewButton = styled(ButtonStyle)`
  right: 1.4%;
  background-color: #59bffd;
  color: #ffffff;
  font-size: 18px;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  transition: all 0.3s;
  &:hover {
    transform: scale(1.1);
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  }
`;

const EditButton = styled(ButtonStyle)`
  color: #79797b;
  background-color: #fafafa;
`;

const DeleteButton = styled(ButtonStyle)`
  color: #dd6868;
  background-color: #fdf6f7;
`;

export default StudentPresenter;
