import styled from "styled-components";

const TeacherModifyPresenter = ({
  handleChangeOfficeHours,
  handleChangeTimes,
  handleChangeSubject,
  handleSubmit,
  teacher,
  subject,
  days,
  times,
  officeHours,
  inputName,
  inputPhone,
  validate,
  errors
}) => (
  <Main>
    <ModalSection>
      <ModalTitle>  </ModalTitle>
      <ModalNameSubject>
        <ModalInput ref={inputName} defaultValue={teacher && teacher.name} type="text" placeholder="" />
        <ModalSelect onChange={handleChangeSubject} value={subject} name="subject" placeholder="">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </ModalSelect>
      </ModalNameSubject>
      <ModalPhone ref={inputPhone} defaultValue={teacher && teacher.phone} type="text" placeholder="" />
    </ModalSection>
    <ModalSection>
      <ModalTitle> / </ModalTitle>
      {days.map((day, index) => (
        <span key={`teacherModifyDayCheckBox${index}`}>
          <input
            type="checkbox"
            id={`day${index}`}
            name="officeHours"
            value={day}
            onChange={handleChangeOfficeHours}
            checked={
              officeHours &&
              officeHours.length > 0 &&
              officeHours.findIndex((officeHour) => officeHour.day === day) > -1
            }
          />
          <label htmlFor={`day${index}`}>
            <span></span> <div className="cboxlabel"> {day} </div>{" "}
          </label>
        </span>
      ))}
      {officeHours &&
        officeHours.length > 0 &&
        officeHours.map((officeHour) => (
          <div className="container" key={`teacherModifyOfficeHour${days.indexOf(officeHour.day)}`}>
            <ul className="ks-cboxtags">
              {times.map((time, tIndex) => (
                <li key={`teacherModifyTime${tIndex}`}>
                  <input
                    type="checkbox"
                    id={`day${days.indexOf(officeHour.day)}time${tIndex}`}
                    name="times"
                    value={`${officeHour.day}${time}`}
                    onChange={handleChangeTimes}
                    // 21.11.11 :  
                    defaultChecked
                    // checked={officeHour.times.includes(time)}
                  />
                  <label htmlFor={`day${days.indexOf(officeHour.day)}time${tIndex}`}>
                    {officeHour.day}
                    {time}
                  </label>
                </li>
              ))}
            </ul>
          </div>
        ))}
    </ModalSection>

    { Object.keys(errors).length === 0 
      ? null 
      : Object.keys(errors).map((key, index)=>(
        <ModalFormError> <i class="fas fa-exclamation-circle"></i> &nbsp; {errors[key]} </ModalFormError>  
      )) 
    }
    <SubmitButton onClick={validate}>  </SubmitButton>
  </Main>
);

const SubmitButton = styled.button`
  display: block;
  background-color: #eef5f9;
  color: black;
  height: 58px;
  border-radius: 4px;
  margin-top: 7px;
  font-size: 15px;
  line-height: 58px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: 0;
  &:hover {
    background-color: #75bdf8;
    color: #fafafa;
    transition-duration: 0.3s;
  }
`;

const ModalNameSubject = styled.div`
  display: flex;
`;

const ModalTitle = styled.div`
  font-size: 14px;
  line-height: 28px;
  font-weight: 800;
  margin-bottom: 10px;
`;

const ModalInput = styled.input`
  flex-basis: 70%;
  width: 100%;
  height: 40px;
  padding-left: 17px;
  border: none;
  border-bottom: 1px solid #efefef;
  margin: 7px 0px;
`;

const ModalPhone = styled.input`
  width: -webkit-fill-available;
  height: 40px;
  padding-left: 17px;
  border: none;
  border-bottom: 1px solid #efefef;
  margin: 7px 0px;
`;

const ModalFormError = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #ff4e5d;
  color: #fff;
  padding: 8px;
  font-size: 13px;
  box-sizing: border-box;
  border-radius: 5px;
  margin-top: 10px;
`;

const ModalSelect = styled.select`
  border: none;
  padding: 5px 10px;
  font: inherit;
  width: 100%;
  font-size: 12px;
  flex-basis: 30%;
`;

const Main = styled.main`
  padding: 35px 35px 35px;
`;

const ModalSection = styled.div`
  margin-bottom: 32px;
`;

export default TeacherModifyPresenter;
