import React, { useState, useRef, useEffect } from 'react';
import PersonalTablePresenter from './PersonalTablePresenter';
import { studentAPI, teacherAPI, tableAPI } from 'api';

const days = ['', '', '', '', '', '', ''];
const times = ['A', 'B', 'C', 'D', 'E', 'F'];

const PersonalTableContainer = () => {
  const [loading, setLoading] = useState(false);
  const [dayIndex, setDayIndex] = useState(0); // , , , , , , 
  const [category, setCategory] = useState(''); // student, teacher
  const [personId, setPerson] = useState(''); // id
  const [table, setTable] = useState();
  const [active, setActive] = useState();
  const [personalTable, setPersonalTable] = useState();
  const [students, setStudents] = useState([]);
  const [teachers, setTeachers] = useState([]);
  const [openShare, setOpenShare] = useState(false);
  const [CSVHeaders, setCSVHeaders] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const getDailyData = async () => {
    setLoading(true);
    try {
      const {
        data: { table: tableData }
      } = await tableAPI.readTable();
      const {
        data: { students: studentsData }
      } = await studentAPI.readAllStudents();
      const {
        data: { teachers: teachersData }
      } = await teacherAPI.readAllTeachers();

      setTable(tableData);
      setActive(tableData.active);
      setStudents(studentsData);
      setTeachers(teachersData);

      //  Date.getDay()  :0, :1, :6
      let dayIndex = new Date().getDay() - 1;
      if (dayIndex < 0) dayIndex = days.length - 1;
      setDayIndex(dayIndex);
    } catch {
    } finally {
      setLoading(false);
    }
  };

  const getPersonalTable = () => {
    if (category === 'students') {
      const pt = [{}, {}, {}, {}, {}, {}, {}];
      table.group &&
        table.group.length > 0 &&
        table.group.forEach((row) => {
          row.lecture.forEach((day, dayIndex) => {
            for (let [time, value] of Object.entries(day)) {
              const studentIdx =
                value && value.students && value.students.findIndex((student) => String(student._id) === personId);
              if (studentIdx > -1) {
                pt[dayIndex][time] = row.teacher;
                pt[dayIndex][time].type = 0;
              }
            }
          });
        });
      table.personal &&
        table.personal.length > 0 &&
        table.personal.forEach((row) => {
          // 16
          row.lecture.forEach((day, dayIndex) => {
            // 7
            for (let [time, value] of Object.entries(day)) {
              // 6
              if (value.student && value.student._id === personId) {
                pt[dayIndex][time] = row.teacher;
                pt[dayIndex][time].type = 1;
              }
            }
          });
        });
      setPersonalTable(pt);
    } else {
      const pt = [];
      const gPersonalTable = [];
      const pPersonalTable = [];
      const teacherIndexInGroupTable =
        table.group && table.group.length > 0 && table.group.findIndex((row) => String(row.teacher._id) === personId);
      if (teacherIndexInGroupTable > -1)
        table.group &&
          table.group.length > 0 &&
          table.group[teacherIndexInGroupTable].lecture &&
          table.group[teacherIndexInGroupTable].lecture.forEach((day) => {
            const obj = new Object();
            for (let [time, value] of Object.entries(day)) {
              if (value) {
                obj[time] = { name: '', type: 0 };
                obj[time].name = value.name;
                obj[time].type = 0;
              }
            }
            gPersonalTable.push(obj);
          });
      const teacherIndexInPersonalTable =
        table.personal &&
        table.personal.length > 0 &&
        table.personal.findIndex((row) => String(row.teacher._id) === personId);
      table.personal &&
        table.personal.length > 0 &&
        table.personal[teacherIndexInPersonalTable] &&
        table.personal[teacherIndexInPersonalTable].lecture &&
        table.personal[teacherIndexInPersonalTable].lecture.forEach((day) => {
          // 7
          const obj = new Object();
          for (let [time, value] of Object.entries(day)) {
            if (value.student) {
              obj[time] = value.student;
              obj[time].type = 1;
            }
          }
          pPersonalTable.push(obj);
        });
      for (let i = 0; i < pPersonalTable.length; i++) {
        const dailyTableData = Object.assign(pPersonalTable[i], gPersonalTable[i]);
        pt.push(dailyTableData);
      }
      setPersonalTable(pt);
    }
  };

  const handleChangeCategory = ({ target: { selectedOptions } }) => {
    const category = selectedOptions[0].value;
    setPerson('');
    setCategory(category);
    setPersonalTable(null);
  };

  const handleChangePerson = ({ target: { selectedOptions } }) => {
    const personId = selectedOptions[0].value;
    setPerson(personId);
  };

  const getCSVData = () => {
    const engDays = ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun'];
    const data = [];
    times.forEach((time) => {
      const row = { time, lecture: { mon: {}, tue: {}, wed: {}, thu: {}, fri: {}, sat: {}, sun: {} } };
      personalTable &&
        personalTable.forEach((day, dayIdx) => {
          row.lecture[engDays[dayIdx]] =
            category === 'students'
              ? day[time] && day[time].type
                ? `${day[time].subject}(${day[time].name})`
                : day[time] && `${day[time].subject}(${day[time].name})`
              : day[time] && day[time].type
              ? day[time].name
              : day[time] && `${day[time].name}`;
        });
      data.push(row);
    });
    setCSVData(data);
  };

  const getCSVHeaders = () => {
    const csvHeaders = [
      { label: '', key: 'time' },
      { label: '', key: 'lecture.mon' },
      { label: '', key: 'lecture.tue' },
      { label: '', key: 'lecture.wed' },
      { label: '', key: 'lecture.thu' },
      { label: '', key: 'lecture.fri' },
      { label: '', key: 'lecture.sat' },
      { label: '', key: 'lecture.sun' }
    ];

    setCSVHeaders(csvHeaders);
  };

  useEffect(() => {
    getDailyData();
    getCSVHeaders();
  }, []);

  useEffect(() => {
    if (category && personId) {
      getPersonalTable();
    }
  }, [category, personId]);

  useEffect(() => {
    getCSVData();
  }, [personalTable]);

  const selectPerson = useRef();

  return (
    <PersonalTablePresenter
      openShare={openShare}
      setOpenShare={setOpenShare}
      dayIndex={dayIndex}
      category={category}
      personId={personId}
      selectPerson={selectPerson}
      students={students}
      teachers={teachers}
      active={active}
      personalTable={personalTable}
      handleChangeCategory={handleChangeCategory}
      handleChangePerson={handleChangePerson}
      CSVHeaders={CSVHeaders}
      CSVData={CSVData}
    />
  );
};

export default PersonalTableContainer;
