import styled from "styled-components";

const Buttons = styled.div`
  display: flex;
  gap: 20px;
`;

const DeleteButton = styled.button`
  display: block;
  background-color: #fdf6f7;
  color: black;
  height: 58px;
  border-radius: 4px;
  margin-top: 40px;
  font-size: 15px;
  line-height: 58px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: 0;
  &:hover {
    background-color: #cd807f;
    color: #fafafa;
    transition-duration: 0.3s;
  }
`;

const SubmitButton = styled.button`
  display: block;
  background-color: #eef5f9;
  color: black;
  height: 58px;
  border-radius: 4px;
  margin-top: 40px;
  font-size: 15px;
  line-height: 58px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: 0;
  &:hover {
    background-color: #75bdf8;
    color: #fafafa;
    transition-duration: 0.3s;
  }
`;

const ModalInfo = styled.div`
  flex-basis: 70%;
`;

const ModalTitle = styled.div`
  flex-basis: 30%;
  font-weight: 800;
`;

const Main = styled.main`
  padding: 35px 35px 25px;
`;

const ModalSection = styled.div`
  display: flex;
  gap: 20px;
  margin-bottom: 32px;
  line-height: 28px;
  font-size: 15px;
`;

const TeacherDetailPresenter = ({ setModalOpen, setModalID, setModalData, handleDelete, teacher }) => (
  <Main>
    <ModalSection>
      <ModalTitle>  </ModalTitle>
      <ModalInfo> {teacher && teacher.subject} </ModalInfo>
    </ModalSection>

    <ModalSection>
      <ModalTitle>  </ModalTitle>
      <ModalInfo> {teacher && teacher.phone} </ModalInfo>
    </ModalSection>
    <ModalSection>
      <ModalTitle> / </ModalTitle>
      <ModalInfo>
        {teacher &&
          teacher.officeHours &&
          teacher.officeHours.length > 0 &&
          teacher.officeHours.map((hour, index) => (
            <span key={`teacherDetaliOfficeHour${index}`}>
              {" "}
              {hour.day}({hour.times}){" "}
            </span>
          ))}
      </ModalInfo>
    </ModalSection>

    <Buttons>
      <SubmitButton
        onClick={(e) => {
          setModalOpen(true);
          setModalID("TeacherModify");
          setModalData(teacher);
          e.stopPropagation();
        }}
      >
        {" "}
        {" "}
      </SubmitButton>
      <DeleteButton onClick={() => handleDelete(teacher && teacher._id)}>  </DeleteButton>
    </Buttons>
  </Main>
);

export default TeacherDetailPresenter;
