import React, { useState, useEffect } from "react";
import TeacherPresenter from "./TeacherPresenter";
import { teacherAPI } from "api";
import HandleConfirm from "Components/HandleConfirm";

const TeacherContainer = () => {
  const [teachers, setTeachers] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalID, setModalID] = useState("");
  const [modalData, setModalData] = useState(null);

  const modalClose = () => {
    setModalOpen(false);
    setModalID("");
    setModalData(null);
  };

  const getTeachersData = async () => {
    try {
      const {
        data: { teachers: _teachers },
      } = await teacherAPI.readAllTeachers();
      setTeachers(_teachers);
    } catch {}
  };

  const deleteTeacher = async (id) => {
    try {
      setModalID("");
      const {
        data: { result },
      } = await teacherAPI.deleteTeacher(id);
      if (result) {
        alert(" ");
      }
    } catch {
    } finally {
      setModalID("FOR RERENDERING");
    }
  };

  const handleDelete = (id) => {
    HandleConfirm("?", () => deleteTeacher(id), null);
  };

  useEffect(() => {
    getTeachersData();
  }, [modalOpen, modalID]);

  return (
    <TeacherPresenter
      modalOpen={modalOpen}
      setModalOpen={setModalOpen}
      teachers={teachers}
      modalID={modalID}
      setModalID={setModalID}
      modalData={modalData}
      setModalData={setModalData}
      modalClose={modalClose}
      handleDelete={handleDelete}
    />
  );
};

export default TeacherContainer;
