import React, { useState, useEffect } from 'react';
import TablePresenter from './TablePresenter';
import { tableAPI } from 'api';

const TableContainer = () => {
  const [loading, setLoading] = useState(false);
  const [openShare, setOpenShare] = useState(false);
  const [table, setTable] = useState();
  const [active, setActive] = useState(false);
  const [CSVHeaders, setCSVHeaders] = useState([]);
  const [CSVData, setCSVData] = useState([]);

  const days = ['', '', '', '', '', '', ''];
  const times = ['A', 'B', 'C', 'D', 'E', 'F'];

  const getTable = async () => {
    try {
      setLoading(true);

      const {
        data: { table: tableData }
      } = await tableAPI.readTable();

      setTable(tableData);
      setActive(tableData.active);
      getCSVData(tableData);
    } catch {
    } finally {
      setLoading(false);
    }
  };

  const handleRefresh = async () => {
    try {
      setLoading(true);
      const {
        data: { table: tableData }
      } = table ? await tableAPI.createTable(table._id) : await tableAPI.createTable();
      setTable(JSON.parse(JSON.stringify(tableData)));
      setActive(tableData.active);
      getCSVData(tableData);
    } catch {
    } finally {
      setLoading(false);
    }
  };

  const handleToggle = async () => {
    try {
      const {
        data: { result }
      } = await tableAPI.changeActiveness(table && table._id, !active);
      console.log(result);
      setActive(!active);
    } catch {}
  };

  const handleClickCell = (e) => {
    const dayIndex = e.currentTarget.getAttribute('data-day-index');
    const time = e.currentTarget.getAttribute('data-time');
    const teacherId = e.currentTarget.getAttribute('data-teacher-id');
    updateCellIsFixed(dayIndex, time, teacherId);
  };

  const updateCellsIsFixed = async () => {
    try {
      const {
        data: { result }
      } = await tableAPI.updateCellsIsFixed(table._id);
      setTable(JSON.parse(JSON.stringify(result)));
    } catch {}
  };

  const updateCellIsFixed = async (dayIndex, time, teacherId) => {
    const teacherIdx = table.personal.findIndex((row) => row.teacher._id === teacherId);

    const cell = table.personal[teacherIdx].lecture[dayIndex][time];
    if (!cell) return;
    try {
      const {
        data: { result }
      } = await tableAPI.updateCellIsFixed(table._id, dayIndex, time, teacherId);
      setTable(JSON.parse(JSON.stringify(result)));
    } catch {}
  };

  const getCSVData = (tableData) => {
    const engDays = ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun'];
    const data = [];
    tableData.group.forEach((row) => {
      const group = {
        type: '',
        teacher: `${row.teacher.name}(${row.teacher.subject})`,
        lecture: { mon: {}, tue: {}, wed: {}, thu: {}, fri: {}, sat: {}, sun: {} }
      };
      row.lecture.forEach((day, dayIdx) => {
        for (const time in day) {
          if (day[time].name) {
            const students = day[time].students.map((student) => student.name);
            group.lecture[engDays[dayIdx]][time] = `${day[time].name}(${students})`;
          }
        }
      });
      data.push(group);
    });

    tableData.personal.forEach((row) => {
      const personal = {
        type: '',
        teacher: `${row.teacher.name}(${row.teacher.subject})`,
        lecture: { mon: {}, tue: {}, wed: {}, thu: {}, fri: {}, sat: {}, sun: {} }
      };
      row.lecture.forEach((day, dayIdx) => {
        for (const time in day) {
          if (day[time].student) {
            personal.lecture[engDays[dayIdx]][time] = day[time].student.name;
          }
        }
      });
      data.push(personal);
    });

    setCSVData(data);
  };

  const getCSVHeaders = () => {
    const csvHeaders = [
      { label: '', key: 'type' },
      { label: '', key: 'teacher' },
      { label: 'A', key: 'lecture.mon.A' },
      { label: 'B', key: 'lecture.mon.B' },
      { label: 'C', key: 'lecture.mon.C' },
      { label: 'D', key: 'lecture.mon.D' },
      { label: 'E', key: 'lecture.mon.E' },
      { label: 'F', key: 'lecture.mon.F' },

      { label: 'A', key: 'lecture.tue.A' },
      { label: 'B', key: 'lecture.tue.B' },
      { label: 'C', key: 'lecture.tue.C' },
      { label: 'D', key: 'lecture.tue.D' },
      { label: 'E', key: 'lecture.tue.E' },
      { label: 'F', key: 'lecture.tue.F' },

      { label: 'A', key: 'lecture.wed.A' },
      { label: 'B', key: 'lecture.wed.B' },
      { label: 'C', key: 'lecture.wed.C' },
      { label: 'D', key: 'lecture.wed.D' },
      { label: 'E', key: 'lecture.wed.E' },
      { label: 'F', key: 'lecture.wed.F' },

      { label: 'A', key: 'lecture.thu.A' },
      { label: 'B', key: 'lecture.thu.B' },
      { label: 'C', key: 'lecture.thu.C' },
      { label: 'D', key: 'lecture.thu.D' },
      { label: 'E', key: 'lecture.thu.E' },
      { label: 'F', key: 'lecture.thu.F' },

      { label: 'A', key: 'lecture.fri.A' },
      { label: 'B', key: 'lecture.fri.B' },
      { label: 'C', key: 'lecture.fri.C' },
      { label: 'D', key: 'lecture.fri.D' },
      { label: 'E', key: 'lecture.fri.E' },
      { label: 'F', key: 'lecture.fri.F' },

      { label: 'A', key: 'lecture.sat.A' },
      { label: 'B', key: 'lecture.sat.B' },
      { label: 'C', key: 'lecture.sat.C' },
      { label: 'D', key: 'lecture.sat.D' },
      { label: 'E', key: 'lecture.sat.E' },
      { label: 'F', key: 'lecture.sat.F' },

      { label: 'A', key: 'lecture.sun.A' },
      { label: 'B', key: 'lecture.sun.B' },
      { label: 'C', key: 'lecture.sun.C' },
      { label: 'D', key: 'lecture.sun.D' },
      { label: 'E', key: 'lecture.sun.E' },
      { label: 'F', key: 'lecture.sun.F' }
    ];

    setCSVHeaders(csvHeaders);
  };

  useEffect(() => {
    let isMounted = true;
    if (isMounted) {
      getTable();
      getCSVHeaders();
    }
    return () => (isMounted = false);
  }, []);

  return (
    <TablePresenter
      loading={loading}
      openShare={openShare}
      setOpenShare={setOpenShare}
      active={active}
      setActive={setActive}
      table={table}
      handleToggle={handleToggle}
      handleRefresh={handleRefresh}
      handleClickCell={handleClickCell}
      updateCellsIsFixed={updateCellsIsFixed}
      CSVHeaders={CSVHeaders}
      CSVData={CSVData}
    />
  );
};

export default TableContainer;
