import React, { useState, useEffect, useRef } from 'react';
import GroupModifyPresenter from './GroupModifyPresenter';
import { groupAPI, studentAPI, teacherAPI } from 'api';
import HandleConfirm from 'Components/HandleConfirm';

const GroupModifyContainer = ({ modalClose, groupData }) => {
  const days = ['', '', '', '', '', '', ''];
  const times = ['A', 'B', 'C', 'D', 'E', 'F'];
  const [group, setGroup] = useState();
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [selectedTeacher, setSelectedTeacher] = useState();
  const [officeHours, setOfficeHours] = useState([]);
  const [students, setStudents] = useState([]);
  const [teachers, setTeachers] = useState([]);
  const [subject, setSubject] = useState('');
  const [isModify, setIsModify] = useState(false);
  const [errors, setErrors] = useState(new Object());
  const handleChangeOfficeHours = ({ currentTarget: target }) => {
    const { value, checked } = target;
    const index = officeHours.findIndex((officeHour) => officeHour.day === value);
    if (checked && index < 0) officeHours.push({ day: value, times: [] });
    if (!checked && index > -1) officeHours.splice(index, 1);
    officeHours.sort((a, b) => (days.indexOf(a.day) >= days.indexOf(b.day) ? 1 : -1));
    setOfficeHours(JSON.parse(JSON.stringify(officeHours)));
  };

  const handleChangeTimes = ({ currentTarget: target }) => {
    const { value, checked } = target;
    const [day, time] = value.split('');
    const dayIndex = officeHours.findIndex((officeHour) => officeHour.day === day);
    if (dayIndex > -1) {
      const timeIndex = officeHours[dayIndex].times.findIndex((t) => t === time);
      if (checked && timeIndex < 0) officeHours[dayIndex].times.push(time);
      if (!checked && timeIndex > -1) officeHours[dayIndex].times.splice(timeIndex, 1);
      officeHours[dayIndex].times.sort((a, b) => (times.indexOf(a) >= times.indexOf(b) ? 1 : -1));
    }
    setOfficeHours(JSON.parse(JSON.stringify(officeHours)));
  };

  const checkOfficeHours = (person, officeHours) => {
    for (let i = 0; i < officeHours.length; i++) {
      const dayIdx = person.officeHours.findIndex((officeHour) => officeHour.day === officeHours[i].day);
      if (dayIdx < 0) return false;
      for (let time of officeHours[i].times) {
        if (!person.officeHours[dayIdx].times.includes(time)) return false;
      }
    }
    return true;
  };

  const checkConditions = () => {
    let result = true;

    if (selectedTeacher) {
      selectedTeacher.isAvailable = checkOfficeHours(selectedTeacher, officeHours);
      if (!selectedTeacher.isAvailable) result = false;
      setSelectedTeacher(selectedTeacher);
    }

    for (const teacher of teachers) teacher.isAvailable = checkOfficeHours(teacher, officeHours);
    setTeachers(JSON.parse(JSON.stringify(teachers)));

    for (const student of students) student.isAvailable = checkOfficeHours(student, officeHours);
    setStudents(JSON.parse(JSON.stringify(students)));

    if (selectedStudents && selectedStudents.length > 0) {
      for (const selectedStudent of selectedStudents) {
        selectedStudent.isAvailable = checkOfficeHours(selectedStudent, officeHours);
        if (!selectedStudent.isAvailable) result = false;
      }

      setSelectedStudents(JSON.parse(JSON.stringify(selectedStudents)));
    }

    return result;
  };

  const handleChangeSubject = (e) => {
    setSubject(e.target.value);
    setSelectedTeacher();
  };

  const handleChangeTeacher = ({ target: { selectedOptions } }) => {
    const teacher = teachers[selectedOptions[0].value];
    setSelectedTeacher(teacher);
  };

  const handleSelectStudent = (e) => {
    const studentIndex = e.target.value;
    const student = students.splice(studentIndex, 1)[0];
    student.isAvailable = checkOfficeHours(student, officeHours);

    selectedStudents.push(student);
    selectedStudents.sort((a, b) => (a.name > b.name ? 1 : -1));
    e.target.value = '';

    setSelectedStudents(JSON.parse(JSON.stringify(selectedStudents)));
  };

  const handleRemoveStudent = (e) => {
    e.stopPropagation();
    let temp = [...selectedStudents];
    let index = temp.findIndex((obj) => obj._id === e.target.id);

    if (index > -1) {
      const student = temp.splice(index, 1)[0];

      setSelectedStudents(temp);
      students.push(student);
      students.sort((a, b) => (a.name >= b.name ? 1 : -1));
    }
  };

  const getStudentsData = async () => {
    try {
      const {
        data: { students: studentsData }
      } = await studentAPI.readAllStudents();
      for (const student of studentsData) student.isAvailable = true;

      //    
      if (groupData && groupData.students.length > 0) {
        groupData.students.forEach((selectedStudent) => {
          const index = studentsData.findIndex((student) => String(student._id) === String(selectedStudent._id));
          studentsData.splice(index, 1);
        });
      }

      if (groupData && groupData.officeHours.length > 0) {
        for (const student of studentsData) student.isAvailable = checkOfficeHours(student, groupData.officeHours);
      }

      setStudents(studentsData);
    } catch {}
  };

  const getTeachersDataBySubject = async () => {
    try {
      const {
        data: { teachers: teachersData }
      } = await teacherAPI.readTeachersBySubject(subject);
      for (const teacher of teachersData) teacher.isAvailable = checkOfficeHours(teacher, officeHours);
      setTeachers(teachersData);
    } catch {}
  };

  const createGroup = async (group) => {
    try {
      const result = await groupAPI.createGroup(group);
      if (result) {
        alert('  ');
        modalClose();
      }
    } catch {}
  };

  const updateGroup = async (group) => {
    try {
      const result = await groupAPI.updateGroup(group);
      if (result) {
        alert('  ');
        modalClose();
      }
    } catch {}
  };

  const validate = () => {
    const temp_errors = {};
    const newGroup = {
      name: inputName.current.value,
      teacher: selectedTeacher,
      officeHours,
      students: selectedStudents
    };

    for (var key in newGroup) {
      // Input  
      if (newGroup[key] === undefined || newGroup[key].length === 0) {
        if (key === 'name') {
          temp_errors[key] = '  .';
        } else if (key === 'teacher') {
          temp_errors[key] = ' .';
        } else if (key === 'officeHours') {
          temp_errors[key] = '  .';
        } else if (key === 'students') {
          temp_errors[key] = ' .';
        }
      }

      if (key === 'students') {
        if (newGroup[key].length == 1) {
          temp_errors[key] = ' 2   .';
        }
      }

      //   (A~F)    
      if (key === 'officeHours') {
        for (var i = 0; i < newGroup[key].length; i++) {
          if (newGroup[key].length != 0 && newGroup[key][i].times.length === 0) {
            temp_errors[key] = newGroup[key][i].day + '  .';
          }
        }
      }
    }

    //   handleSubmit
    if (Object.keys(temp_errors).length === 0 && temp_errors.constructor === Object) {
      handleSubmit(newGroup);
    } else {
      setErrors({ ...temp_errors });
    }
  };

  const handleSubmit = (newGroup) => {
    if (isModify) newGroup._id = group._id;
    HandleConfirm('?', () => (isModify ? updateGroup(newGroup) : createGroup(newGroup)), null);
  };

  useEffect(() => {
    let isMounted = true;
    if (isMounted) {
      groupData && setIsModify(true);
      groupData && setGroup(groupData);
      groupData && setSubject(groupData.teacher.subject);
      groupData && setSelectedTeacher(groupData.teacher);
      groupData && setSelectedStudents(groupData.students);
      groupData && setOfficeHours(groupData.officeHours);
      getStudentsData();
    }
    return () => (isMounted = false);
  }, []);

  useEffect(() => {
    checkConditions();
    if (selectedTeacher && !checkOfficeHours(selectedTeacher, officeHours)) {
      setSelectedTeacher();
    }
  }, [officeHours]);

  useEffect(() => {
    subject && getTeachersDataBySubject();
  }, [subject]);

  const inputName = useRef();

  return (
    <GroupModifyPresenter
      group={group}
      days={days}
      times={times}
      subject={subject}
      selectedStudents={selectedStudents}
      selectedTeacher={selectedTeacher}
      handleChangeOfficeHours={handleChangeOfficeHours}
      handleChangeTimes={handleChangeTimes}
      handleSelectStudent={handleSelectStudent}
      handleChangeTeacher={handleChangeTeacher}
      handleRemoveStudent={handleRemoveStudent}
      handleChangeSubject={handleChangeSubject}
      handleSubmit={handleSubmit}
      students={students}
      teachers={teachers}
      officeHours={officeHours}
      days={days}
      times={times}
      inputName={inputName}
      validate={validate}
      errors={errors}
    />
  );
};

export default GroupModifyContainer;
