import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { withRouter } from "react-router-dom";
//  
import StudentDetail from "Routes/StudentDetail";
import StudentModify from "Routes/StudentModify";
import TeacherDetail from "Routes/TeacherDetail";
import TeacherModify from "Routes/TeacherModify";
import GroupModify from "Routes/GroupModify";

const ModalContainer = styled.div`
  display: ${(props) => (props.open ? "flex" : "none")};
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
  animation: modal-bg-show 0.3s;
  ${(props) => (props.open ? "animation-direction: reverse" : "")}

  @keyframes modal-bg-show {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
`;

const Modal = styled.section`
  width: 100%;
  height: fit-content;
  min-height: 550px;
  max-width: ${(props) => (props.id.includes("Teacher") || props.id.includes("Detail") ? "35%" : "75%")};
  margin: 0 auto;
  border-radius: 0.3rem;
  background-color: #fff;
  animation: modal-show 0.3s;
  overflow: hidden;

  @keyframes modal-show {
    from {
      opacity: 0;
      margin-top: -50px;
    }
    to {
      opacity: 1;
      margin-top: 0;
    }
  }
`;

const Header = styled.header`
  position: relative;
  margin-top: 60px;
  margin-bottom: 15px;
  color: #3c3c3c;
  font-weight: 900;
  font-size: 18px;
  text-align: center;
`;

const CloseButton = styled.button`
  margin-top: -40px;
  position: absolute;
  right: 20px;
  width: 30px;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  color: #999;
  background-color: transparent;
  display: block;
  outline: none;
  cursor: pointer;
  border: 0;
`;


function ModalHeader({modalData, modalID}) {
  if (modalID === "StudentRegister"){
    return (<>    </>)
  } else if(modalID==="StudentModify"){
    return (<>    </>)
  } else if(modalID === ("TeacherRegister")){
    return (<>    </>)
  } else if(modalID==="TeacherModify"){
    return (<>    </>)
  } else if (modalID === "GroupRegister"){
    return(<>   </>)
  } else if(modalID === "GroupModify"){
    return(<>   </>)
  } else if (modalID === "StudentDetail" || modalID == "TeacherDetail"){
    return(<> { modalData && modalData.name } </>)
  } 
  return (<> </>)
}

const ModalComponent = ({
  modalOpen,
  setModalOpen,
  modalID,
  setModalID,
  modalData,
  setModalData,
  modalClose,
  header,
}) => {
  // modal   container  

  const modalEl = useRef();
  const handleClickOutside = ({ target }) => {
    if (modalEl && modalEl.current && !modalEl.current.contains(target)) modalClose(); //    
  };
  useEffect(() => {
    window.addEventListener("click", handleClickOutside);
    return () => {
      window.removeEventListener("click", handleClickOutside);
    };
  }, []);

  return (
    <ModalContainer open={modalOpen}>
      <Modal ref={modalEl} id={modalID}>
        <Header>
          <CloseButton onClick={modalClose}> &times;</CloseButton>
          {/* TODO:  */}
          <ModalHeader modalData={modalData} modalID={modalID}> </ModalHeader> 
          {/* {(modalData && modalData.name) || (modalID && modalID.includes("Student") ? "  " : "  ")} */}
        </Header>
        {/*    */}
        {modalID === "StudentRegister" && <StudentModify modalClose={modalClose} />}
        {modalID === "StudentDetail" && (
          <StudentDetail
            modalClose={modalClose}
            studentData={modalData}
            setModalOpen={setModalOpen}
            setModalID={setModalID}
            setModalData={setModalData}
          />
        )}
        {modalID === "StudentModify" && <StudentModify modalClose={modalClose} studentData={modalData} />}

        {/*    */}
        {modalID === "TeacherRegister" && <TeacherModify modalClose={modalClose} />}
        {modalID === "TeacherDetail" && (
          <TeacherDetail
            modalClose={modalClose}
            teacherData={modalData}
            setModalOpen={setModalOpen}
            setModalID={setModalID}
            setModalData={setModalData}
          />
        )}
        {modalID === "TeacherModify" && <TeacherModify modalClose={modalClose} teacherData={modalData} />}


        {/*    */}
        {modalID === "GroupRegister" && <GroupModify modalClose={modalClose}  /> }
        {modalID === "GroupModify" && <GroupModify modalClose={modalClose} groupData={modalData}/>}
      </Modal>
    </ModalContainer>
  );
};

export default ModalComponent;
